<template>
  <div id="checkoutstyle">
      <!--  -->
      <div v-show="PaySuccessShow">
            <div id="back">
                <span id="tittle" ></span>
            </div>
            <div  id="OrderSuccess">
              <div style="text-align: center">
                <p class="content"> <span class="price"><em style="font-size: 18px"></em>{{PaySum}}</span> </p>
                <router-link to="/OrderPage">
                    <el-button type="primary" size="large"></el-button>
                </router-link>
                <router-link to="/">
                    <el-button type="primary" size="large"></el-button>
                </router-link>
                
              </div>
                  

            </div>
      </div>
      <!--  -->
      <div v-show="OrderSuccessShow">
          <div id="back">
          <span id="tittle" ></span>
          </div>
          <div id="OrderSuccess">
              <div>
                  <div id="tipsShow">
                        <h3 ></h3>
                        <p class="payment-detail"> <span>24 </span></p>
                        <!-- <p class="payment-detail"> 72 </p> -->
                  </div>
                 
                  <div class="pay-type">
                      <div class="p-title"></div>
                      <div class="pay-item">
                        <div :class="{active:payType==1}" @click="payType=1"><img src="/static/images/alipay@2x.png" alt=""></div>
                        <div :class="{active:payType==2}" @click="payType=2"><img src="/static/images/weixinpay@2x.png" alt=""></div>
                      </div>
                  </div>
              </div>
          </div>
          <div>
             <el-button type="success"  style="margin-left:850px" @click="Order_success_next"></el-button>  
          </div>
      </div>

      <!--  -->
      <div v-show="PersonPageShow" >
          <div id="back">
          <span id="tittle" ></span>
          </div>
          <div id="PersonBox">
                <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="" label-width="150px">
                <span>{{ user.user_name}}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ user.login_name}}</span>
              </el-form-item>
              <el-form-item label="" label-width="150px">
                <span>{{ user.userPhoneNumber }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ user.userIDCard }}</span>
              </el-form-item>
              </el-form>
          </div>
          <div>
             <el-button type="success"  style="margin-left:850px" @click="submit_Person_next"></el-button>  
          </div>
      </div>

      <!--  -->
      <div v-show="GoodPageShow">
      <div id="back">
          <span id="tittle" ></span>
      </div>
      <div>
            <el-table :data="checkOutResults"  style="width: 100%">
          <el-table-column  fixed  prop="good_name" label=""  width="220">
          </el-table-column>
          <el-table-column prop="good_date_belong"  label=""  width="120">
              <template scope="scope">
                    {{scope.row.good_date_belong + ''}}
              </template>
          </el-table-column>
          <el-table-column  label="" width="120">
            <template scope="scope">
                {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
            </template>
          </el-table-column>
          
          <el-table-column  prop="goodCnt"  label="" width="120">
            <template scope="scope">
                {{scope.row.goodCnt + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="sumIncome"  label=""  width="120">
            <template scope="scope">
                {{scope.row.sumIncome + ''}}
            </template>
          </el-table-column>

          <el-table-column  prop="good_date_end" label=""  width="250">
            <template scope="scope">
                {{scope.row.good_date_begin.substring(0,10) +'' + scope.row.good_date_end.substring(0,10)}}
            </template>
          </el-table-column>

          <el-table-column  prop="good_date_income"  label=""  width="120">
          </el-table-column>
        </el-table>
      </div>
          <div style="margin-top:20px">
            <el-row :gutter="20" >
              <el-col :span="3"><div class="grid-content bg-purple">
                    <!-- <el-button type="success" width=90 @click="chooseAll"></el-button>   -->
              </div></el-col>
              <el-col :span="9"><div class="grid-content bg-purple"></div></el-col>
              <el-col :span="9"><div class="grid-content bg-purple" style="padding-top:8px;padding-left:0px">
                    <span style="padding-left:20px">:
                      <span style="color:red">
                          {{this.selectGoodsNum +''}}
                      </span>
                    </span>

                    <span style="padding-left:20px" >:
                      <span style="color:red">
                          {{'' + this.PaySum +''}}
                      </span>
                    </span>
              </div></el-col>
              <el-col :span="3"><div class="grid-content bg-purple"  >
                <el-button type="success"  @click="submit_order_next"></el-button>  
              </div></el-col>
          </el-row>
          </div>
        </div>

        <!--   -->
        <el-row :gutter="20">
          <el-col :span="12" :offset="4">
            <div class="grid-content bg-purple">
              <el-steps :space="200" :active="active">
                  <el-step title="" description=""></el-step>
                  <el-step title="" description=""></el-step>
                  <el-step title="" description=""></el-step>
                  <el-step title="" description=""></el-step>
              </el-steps>
            </div>
          </el-col>
        </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        checkOutResults: [], //
         payType: 1,
        checkOutID:0,
        selectGoodsNum:0,
        listLoading:false,
        PaySum:0  , //
        GoodPageShow :true,   //
        PersonPageShow:false,  //
        OrderSuccessShow:false,  //
        PaySuccessShow:false,
        active: 1,
        user:'',
        orderNo:'', //
      }
    },

    methods: {
      submit_order_next() {  // 
        // console.log(this.checkOutResults)
        this.active++;
        this.GoodPageShow = false;
        this.PersonPageShow = true;
      },
      submit_Person_next() { // 
         this.active++; 
         this.PersonPageShow = false;
         this.OrderSuccessShow = true;
      },
      GetRandomNum() {   // 
        var now = new Date();
        var month = now.getMonth() + 1 ;
        this.orderNo = now.getFullYear().toString() + month.toString() + now.getUTCDate().toString() + now.getHours().toString() + now.getUTCMinutes().toString() + now.getUTCSeconds().toString() + Math.round(Math.random()*10).toString();
        // console.log(Math.round(new Date().getTime()/1000));
      },

      Order_success_next(){ //  
        this.active++;
        this.OrderSuccessShow = false;
        this.PaySuccessShow = true;
        // console.log(this.checkOutResults);
        this.GetRandomNum(); //
        let para = { goods : this.checkOutResults, orderNo: this.orderNo, userInfo : this.user }; //
        // console.log(this.orderNo);
          console.log(para); //json  
          this.$http.post('./api/user/InsertIntoOrderTable', {   //{ } json
            paraaaa:                para,
          }, {}).then((response) => {
                  this.$message({
                      message: '',
                      type: 'success'
                  });
                });  
      },

    },
     mounted() {
      var goods = sessionStorage.getItem('goods');
      var user = sessionStorage.getItem('user');
      if(user){
          this.user =JSON.parse(user);
      }
			if (goods) {
              goods = JSON.parse(goods); // 
              if(goods[0].is_resell != '1'){
                for(var index = 0; index < goods.length; index ++ )
                {
                  var element = goods[index];
                  element.is_resell = 0;
                }
              }
              console.log(goods);
              this.checkOutResults = goods;
              var PaySum = 0;
              for (let index = 0; index < goods.length; index++) {
                  var dateEnd = new Date(goods[index].good_date_end);//
                  goods[index].good_date_end = dateEnd.toLocaleDateString(); //
                  var dataIncome = new Date(goods[index].good_date_income);
                  goods[index].good_date_income = dataIncome.toLocaleDateString(); 
                  var sum = (goods[index].goodCnt * goods[index].good_income_rate / 365.0 ) * goods[index].good_date_belong; // 
                  goods[index].sumIncome = sum.toFixed(2); //
                  PaySum = PaySum + goods[index].goodCnt;
                  this.selectGoodsNum ++;
               }
              this.checkOutResults = goods;
              this.PaySum = PaySum;
			}
		}
  }
</script>

<style lang="scss" scoped rel="stylesheet/scss">
    	#checkoutstyle{
        width: 1000px;
        margin: 0 auto;
        height: 500px;
      }
      #tittle{
        font-size: 18px;
        font-weight: 400;
        color: #626262;
        display: inline-block;
      }
      #back{   //
        height: 60px;
        z-index: 10;
        line-height: 60px;
        font-size: 18px;
        color: #333;
        display: flex;
        justify-content: space-between;
        padding: 0 10px 0 24px;
        align-items: center;
        border-bottom: 1px solid #d4d4d4;
        background: linear-gradient(#fbfbfb, #ececec);
        background: -webkit-linear-gradient(#fbfbfb, #ececec);
      }
      #PersonBox{   // 
        height: 150px;
        border-bottom: 1px solid #d4d4d4;
        border-left: 1px solid #d4d4d4;
        border-right: 1px solid #d4d4d4;
        padding: 50px 0px 0 190px;
        margin-bottom: 30px;
      }

      #OrderSuccess{  // 
        height: 220px;
        border-bottom: 1px solid #d4d4d4;
        border-left: 1px solid #d4d4d4;
        border-right: 1px solid #d4d4d4;
        padding: 40px 0px 0px 0px;
        margin-bottom: 30px;
      }
    /**/
    .pay-type {
      margin: 0 auto;
      width: 95%;
      padding-bottom: 30px;
      .p-title {
        font-size: 18px;
        line-height: 40px;
        padding: 0 10px;
        position: relative;
        &:before {
          content: ' ';
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          border-bottom: 1px solid #ccc;
        }
      }

    }

    .pay-type {
      .pay-item {
        display: flex;
        align-items: center;
        div {
          margin-top: 20px;
          width: 175px;
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: center;
          border: 1px solid #E5E5E5;
          cursor: pointer;
          border-radius: 6px;
          margin-right: 10px;
          background: #FAFAFA;
          &.active {
            border-color: #6A8FE5;
            background: #fff;
          }
          img {
            display: block;
            height: 34px;
            margin: 8px auto;
          }
        }
      }
    }

    #tipsShow{
      // margin-left: 300px;
      text-align: center;
      margin-bottom: 20px;
    }
    
    .payment-detail {
      text-align: center;
      line-height: 24px;
      font-size: 14px;
      color: #999;
    }

     span.price {
    color: #d44d44;
    font-weight: 700;
    line-height: 20px;
    text-align: right;
  }

  .content {
    padding: 20px 0;
    font-size: 30px;
  }
</style>


                    


