<template>
    <div id="main">
        <div> 
            <div id="search" >
                <el-input
                        placeholder=""  icon="search" v-model="search"  :on-icon-click="handleIconClick">
                </el-input> 
            </div>
            <div class="block"  style="float: left;margin-top: 10px;margin-left:10px">
                <el-select v-model="value" placeholder=""  @change="RateChage" clearable  style="width:130px" >
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                <el-select v-model="belongTime" placeholder=""   @change="TimeChange"  clearable style="width:130px" >
                    <el-option
                    v-for="item in optionsTime"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                <el-select v-model="StartMoney" placeholder=""  @change="MoneyChange" clearable  style="width:130px" >
                    <el-option
                    v-for="item in optionsMoney"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
		    </div>
            <div>
                <el-button type="primary" style="float: left;margin-top: 10px;margin-left:10px"  @click="StartMoney='';belongTime='';value='';" ></el-button>
            </div>
        </div>

            <div id="page" >
                <el-tabs v-model="activemoney_begin"  @tab-click="handleClick" v-loading="listLoading"  type="card" >
                     <el-tab-pane label="" name="HaveSoldRank">
                        <el-table :data="results"  border highlight-current-row  
                        v-loading="listLoading" @selection-change="selsChange" style="width: 100%" 
                        :default-sort = "{prop: 'score', order: 'descending'}" >
                            <el-table-column  label=""  type="index" width="70px"  >
                     
                            </el-table-column>
                            <el-table-column  label=""  prop="score"  width="100px" >
                            </el-table-column>
                            <el-table-column  label=""  prop="good_name" >
                            </el-table-column>
                            <el-table-column label=""  prop="good_money_begin"  width="122px" sortable>
                                <template scope="scope">
                                    {{scope.row.good_money_begin + ''}}
                                </template>
                            </el-table-column>
                            <el-table-column label="" prop= "good_date_belong" width="116px"  sortable>
                                <template scope="scope">
                                    {{scope.row.good_date_belong + ''}}
                                </template>
                            </el-table-column>
                            <el-table-column label="" prop= "good_income_rate" width="109px" sortable>
                                <template scope="scope">
                                    {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
                                </template>
                            </el-table-column>

                            <el-table-column  label="" width="145px" >
                                <template scope="scope">
                                    <el-button size="small" type="primary" @click="handIntoCart(scope.row)"></el-button>
                                    <el-button size="small" type="success" :disabled="scope.row.goodSum -scope.row.goodHaveSoldNum <=0"  @click="handleBuy(scope.row)"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                        <el-col :span="24" class="toolbar">
                            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
                            </el-pagination>
                        </el-col>
                     </el-tab-pane>

                    
                    <el-tab-pane label="" name="resellGoods">
                          <el-table :data="results"  border highlight-current-row  
                        v-loading="listLoading" @selection-change="selsChange" style="width: 100%" 
                        :default-sort = "{prop: 'count_have_sold', order: 'descending'}" >
                            <el-table-column  label="#" type="index"  width="70px"  >
                            </el-table-column>
                            <el-table-column  label=""  prop="good_name"  width="227px" >
                            </el-table-column>

                            <el-table-column label=""  prop="have_bought_number" sortable width="120px">
                                <template scope="scope">
                                    {{scope.row.have_bought_number + ''}}
                                </template>
                            </el-table-column>
                            <el-table-column label="" prop= "good_date_belong" sortable width="120px">
                                <template scope="scope">
                                    {{scope.row.good_date_belong + ''}}
                                </template>
                            </el-table-column>
                            <el-table-column label="" prop= "good_income_rate" sortable  width="110px">
                                <template scope="scope">
                                    {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
                                </template>
                            </el-table-column>
                             <el-table-column label="" prop= "goodIncomeNum" sortable width="110px">
                                <template scope="scope">
                                    {{ scope.row.goodIncomeNum  + '' }}
                                </template>
                            </el-table-column>
                             <el-table-column  label="" width="140px">
                                <template scope="scope">
                                     <el-tag type="danger" v-show="scope.row.is_resell == 1"  close-transition>
                                        
                                    </el-tag>
                                </template>
                            </el-table-column>
                            <el-table-column  label="" width="150px" >
                                <template scope="scope">
                                    <el-button size="small" type="primary" @click="handIntoCart(scope.row)"></el-button>
                                    <el-button size="small" type="success" @click="handleBuy(scope.row)"></el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                         <el-col :span="24" class="toolbar" style="heigth:30px">
                            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
                            </el-pagination>
                        </el-col>
                    </el-tab-pane>
                   
                    
                </el-tabs>
          
            </div>
        
            <!--  -->
          <div>
             <!---->
                <el-dialog title="" v-model="BuyFormVisible"   :close-on-click-modal="false">
                <el-form :model="BuyForm" label-width="120px"   ref="BuyForm" >
                    <el-form-item label="" prop="good_name">
                    <span>{{BuyForm.good_name}}</span>
                    </el-form-item>
                    
                    <el-form-item label="">
                    <span></span>
                    </el-form-item>

                    <el-form-item label="" prop="good_date_belong">
                    <span>{{BuyForm.good_date_belong + ''}}</span>
                    </el-form-item>

                    <el-form-item label="" >
                    <span>{{(BuyForm.good_income_rate * 100 ).toFixed(2) + '%'}}</span>
                    </el-form-item>

                    <el-form-item label=""   >
                    <span>{{BuyForm.good_money_begin + '' }}</span>
                    </el-form-item>

                    <el-form-item label=""   >
                    <span>{{BuyForm.good_inc_money + '' }}</span>
                    </el-form-item>
                     <el-form-item label="" prop="goodCnt"  v-if="BuyForm.is_resell == 1">
                        <span>{{BuyForm.goodCnt + '' }}</span>
                    </el-form-item>
                      <el-form-item label="" prop="goodCnt"   v-else
                     :rules="[
                        { required: true, message: ''},
                        ]" >
                        <el-input  style="width:200px" type="number" @change="checkNumberOk"  v-model.number="BuyForm.goodCnt" auto-complete="off" :step=good_inc_num  :min="BuyForm.goodCnt" :max=" BuyForm.goodSum - BuyForm.goodHaveSoldNum   " ></el-input>
                          <el-button   type="text" @click=""></el-button> {{BuyForm.goodSum - BuyForm.goodHaveSoldNum + ''}}
        
                    </el-form-item>
                    <el-form-item label="">
                    <el-checkbox label="" name="type" v-model="checkek" ></el-checkbox>
                    <span  style="width:300px" >  
                        <el-button   type="text" @click="GoodsRiskFormVisible = true"></el-button></span>
                    </el-form-item>

                </el-form>
                
                <div slot="footer" class="dialog-footer" align ="center" >
                    <el-button @click.native="BuyFormVisible = false"></el-button>
                    <el-button type="primary" :disabled=" !checkek || buyDisable " @click.native="BuyInfoSubmit" :loading="CartInsertLoading"></el-button>
                </div>
                </el-dialog> 
        </div>
        <!--  -->
        <div>
              <!--  -->
             <el-dialog title="" v-model="GoodsDetailFormVisible"   :close-on-click-modal="false"  size="large" >
            <el-table :data="showResults"  style="width: 100%; margin-bottom:10px">
          <el-table-column  fixed  prop="good_name" label=""  width="220">
          </el-table-column>
          <el-table-column  prop="good_belonged_bank" label=""  width="220">
          </el-table-column>
          <el-table-column  label="" width="100px">
                  <template scope="scope">
                        <el-tag type="primary" v-if="scope.row.is_resell == 1"  close-transition>
                          
                      </el-tag>
                      <el-tag type="success" v-else  close-transition>
                          
                      </el-tag>
                  </template>
            </el-table-column>
          <el-table-column prop="good_date_belong"  label=""  width="120">
              <template scope="scope">
                    {{scope.row.good_date_belong + ''}}
              </template>
          </el-table-column>
          <el-table-column prop="good_date_belong"  label=""  width="200">
              <template scope="scope">
                    {{scope.row.goodSum -scope.row.goodHaveSoldNum  + ''}}
              </template>
          </el-table-column>
          <el-table-column  label="" width="120">
            <template scope="scope">
                {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
            </template>
          </el-table-column>
          <el-table-column  prop="good_money_begin"  label="" width="120">
            <template scope="scope">
                {{scope.row.good_money_begin + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_inc_money"  label="" width="120">
            <template scope="scope">
                {{scope.row.good_inc_money + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_end" label=""  width="200">
            <template scope="scope">
                {{scope.row.good_date_begin.substring(0,10) + '' +  scope.row.good_date_end.substring(0,10) }}
            </template>
          </el-table-column>

          <el-table-column  prop="good_date_income"  label=""  width="120">
              <template scope="scope">
                {{scope.row.good_date_income.substring(0,10)}}
            </template>
          </el-table-column>
            <el-table-column  label=""  width="200">
              <template scope="scope">
                
            </template>
          </el-table-column>
        </el-table>
        <div>
             {{good_others}}
        </div>
        <div>
            {{goodBelongPerInfo}}
        </div>                
                <div slot="footer" class="dialog-footer" align ="center" >
                    <el-button @click.native="GoodsDetailFormVisible = false"></el-button>
                </div>
                </el-dialog>        
        </div>

        <div>
                <!--  -->
             <el-dialog title="" v-model="GoodsRiskFormVisible"   :close-on-click-modal="false"  size="large" >
                 <div style="padding-left:50px;padding-right:50px;">
                     <h2 align="center"> </h2>
                    <h2>:</h2>
                    <h4>&nbsp;&nbsp;&nbsp;&nbsp;
                        <br>
                        
                        
                    </h4>
                 </div>
                    
                    <div slot="footer" class="dialog-footer" align ="center" >
                        <el-button @click.native="GoodsRiskFormVisible = false"></el-button>
                    </div>
                </el-dialog>        
        </div>
    </div>
</template>
<script>

export default {
    data() {
			return {
                search :'',
                listLoading: false,  //
                CartLoading: false,  //
                sysUserName:'aaa',
                belongTime:'',//
                StartMoney:'',//
                GoodsRiskFormVisible:false,
                good_others:'',//
                results : [],
                backupResult:[],//
                showResults:[],
                GoodsDetailFormVisible:false,
                total: 20,  // 
                tabName:'', // 
                page: 1,
                activemoney_begin: 'HaveSoldRank', //
                activeIndex: '1',
                rankNumber:1,
                BuyFormVisible:false,   // 
                CartInsertLoading :false, // 
                usrID:'',//ID,
                checkek:false,
                goodRemain:'', //
                good_inc_num: 0 ,  //
                buyDisable:false,
                CartForm :[{
                    goodCnt:''
                }],
                BuyForm :[{
                    goodCnt:''
                }],
                options: [{
                    value: '1',
                    label: '3%'
                    }, {
                    value: '2',
                    label: '3%-%4'
                    }, {
                    value: '3',
                    label: '4%-%5'
                    }, {
                    value: '4',
                    label: '%5-%6'
                    }, {
                    value: '5',
                    label: '%6'
                }],
                 optionsMoney: [{
                    value: '1',
                    label: '1'
                    }, {
                    value: '2',
                    label: '1-5'
                    }, {
                    value: '3',
                    label: '5-10'
                    }, {
                    value: '4',
                    label: '10'
                    }],
                 optionsTime: [{
                    value: '1',
                    label: '90'
                    }, {
                    value: '2',
                    label: '90-180'
                    }, {
                    value: '3',
                    label: '180'
                    }],
                value: '',
                flag:0,
                goodBelongPerInfo:'',//
			}
		},
		methods: {
            selectGoods() {  //
                this.listLoading = true;
                this.$http.post('./api/user/selectGoods', {
                    page: this.page,
                    tabName: this.tabName
                },{}).then((response) => {
                    this.results = response.data.data;
                    this.backupResult = this.results;
                    this.total = response.data.total;
                    this.listLoading = false;
                    console.log(this.results );
                    this.recommedEngine();
                    //  console.log(this.results );
                })
            },
            
            RateChage(){ //
                this.filterRate();
            },
            TimeChange(){ //
                // this.filterRate();
                console.log(this.belongTime);
                var val = this.belongTime;
                if (this.results.length != 0 && val !='') {
                    switch (val) {
                    case '1':
                        this.TimeOperation(0,90);
                        break;
                     case '2':
                        this.TimeOperation(90,180);
                        break;
                    case '3':
                        this.TimeOperation(180,100000); //
                        break;
                    }
                }else{
                    if (this.tabName == '') {
                        this.selectGoods();
                    }else{
                        this.selectResellGoods();
                    }
                    this.belongTime='';
                }
                
            },
            MoneyChange(){ //
                // this.filterRate();
                console.log(this.StartMoney);
                var val = this.StartMoney;
                if (this.results.length != 0 && val !='') {
                     switch (val) {
                    case '1':
                        this.MOneyOperation(0,10000);
                        break;
                     case '2':
                        this.MOneyOperation(10000,50000);
                        break;
                    case '3':
                        this.MOneyOperation(50000,100000);
                        break;
                    case '4':
                        this.MOneyOperation(100000,100000000);
                        break;
                    }
                }else{
                    if (this.tabName == '') {
                        this.selectGoods();
                    }else{
                        this.selectResellGoods();
                    }
                   this.StartMoney='';
                }
                
                
            },
            RateOperation(rateL,rateR){  //
                for(var index = 0; index < this.results.length ; ){
                    var element = this.results[index].good_income_rate;
                    if (element <= rateR  &&  element >= rateL) {
                        index ++;
                        continue;
                    }else{
                        this.results.splice(index,1);
                    }
                }
            },

            TimeOperation(rateL,rateR){  //
            console.log('111');
            
                for(var index = 0; index < this.results.length ; ){
                    var element = this.results[index].good_date_belong;
                    if (element <= rateR  &&  element >= rateL) {
                        index ++;
                        continue;
                    }else{
                        this.results.splice(index,1);
                    }
                }
            },

            MOneyOperation(rateL,rateR){  //
                for(var index = 0; index < this.results.length ; ){
                    var element = this.results[index].good_money_begin;
                    if (element <= rateR  &&  element >= rateL) {
                        index ++;
                        continue;
                    }else{
                        this.results.splice(index,1);
                    }
                }
            },

            checkNumberOk(){//
                var remain = this.BuyForm.goodSum - this.BuyForm.goodHaveSoldNum;
                if (remain < this.BuyForm.goodCnt) { //
                  this.$message.error('');
                  this.buyDisable = true;
                }else{
                    this.buyDisable = false;
                }
            },
            filterRate(){ //
                var val = this.value;
                if (this.results.length != 0 && this.value!='') {
                     switch (val) {
                    case '1':
                        this.RateOperation(0,0.03);
                        break;
                     case '2':
                        this.RateOperation(0.03,0.04);
                        break;
                    case '3':
                        this.RateOperation(0.04,0.05);
                        break;
                    case '4':
                        this.RateOperation(0.05,0.06);
                        break;
                    case '5':
                        this.RateOperation(0.06,1);
                        break;
                    }
                }else{
                    if (this.tabName == '') {
                        this.selectGoods();
                    }else{
                        this.selectResellGoods();
                    }
                    this.value='';
                }
            },
            selectResellGoods() { //
                this.listLoading = true;
                this.$http.post('./api/user/selectResellGoods', {
                    page: this.page,
                },{}).then((response) => {
                    var results = response.data.data;
                    this.total = response.data.total;
                    this.listLoading = false;
                    var date = new Date(new Date().setHours(0, 0, 0, 0)).getTime(); //0
                    for (var index = 0; index < results.length; index++) { //
                        var element = results[index];
                        var endday = new Date(element.good_date_income).getTime();
                        var diffnum = endday - date ;
                        var days=Math.floor(diffnum/(24*3600*1000));
                        element.remainDays = days; //  
                    }  
                    this.results = results;
                })
            },
            handleIconClick() { //
                console.log(this.search);
                var key = this.search;
                 if ( (key  == '' || this.results.length ==0 )  ) {
                        if (this.tabName == '') {
                            this.selectGoods();
                        }else{
                            this.selectResellGoods();
                        }
                    }
                    else{
                         for (var i = 0  ; i < this.results.length ; ) {
                                    if ( this.results[i].good_name.toString().search(key) != -1 ) 	  
                                        {
                                            i++;
                                            continue;
                                        }
                                        else
                                        {
                                            this.results.splice(i,1);
                                            //  length  
                                        }
                                    }

                            }
            },

            handleBuy(buyResult){  //
                console.log(buyResult);
                if(this.usrID == '') //
                {
                    this.$router.push({ path: '/indexlogin' });
                }
                else{
                    //session 
                    this.BuyFormVisible = true;
                    this.BuyForm = buyResult;
                    if(buyResult.is_resell == 1)//
                    {
                         this.BuyForm.goodCnt  = buyResult.have_bought_number;
                    }else{
                         this.BuyForm.goodCnt  = buyResult.good_money_begin;  // 
                    }
                    this.good_inc_num =buyResult.good_inc_money;  
                    // this.$router.push({ path: '/checkOut' }) //
                }
            },
            selsChange: function (sels) {
                // this.sels = sels;
                // console.log(this.sels);
            },
            handleClick(tab) { //   
                this.tabName = tab.label;
                this.page = 1;
                console.log(tab.label);
                if (tab.label == '') {  //
                    this.selectResellGoods();
                } else {
                    this.selectGoods();
                }
            }, 
            handleCurrentChange(val) {  //  
            this.page = val;
            console.log(this.page);
            this.selectGoods();
            }, 
            handIntoCart(cartResult){ //

                this.GoodsDetailFormVisible = true;
                this.good_others = cartResult.good_others;
                this.showResults.length = 0;
                this.showResults.push(cartResult);
                this.goodBelongPerInfo = cartResult.goodBelongPerInfo//
                
                console.log(cartResult);
            },
            BuyInfoSubmit(){
                var arry = new Array();
                this.BuyForm.cartID = ''; //
                arry.push(this.BuyForm);
                // console.log(arry);
                sessionStorage.setItem('goods', JSON.stringify(arry) );
                sessionStorage.setItem('', JSON.stringify(arry) );
                this.$router.push({ path: '/checkOut' }) //
            },
            
            calculateHaveSold(haveSold){ //
                return parseInt(haveSold) % 100 ;
            },

            calculateBelongTime(belongTime){//
               belongTime = parseInt(belongTime);
                if(belongTime < 90){ //90 70
                    return 70;
                }else if(belongTime < 180){ //90-180 80
                    return 80;
                }else{   //18060
                    return 60;
                }
            },
            calculateStartMoney(StartMoney){//
                StartMoney = parseInt(StartMoney);
                 if(StartMoney < 10000){ //10000 70
                    return 70;
                }else if(StartMoney <= 50000 && StartMoney >= 10000){ //1 - 5 80
                    return 80;
                }else if(StartMoney <= 100000 && StartMoney > 50000){   //5 -10 60
                    return 60;
                }else {
                    return 50;
                }
            },
            calculateRateNumber(RateNumber){//
                 RateNumber = parseFloat(RateNumber);
                 if(RateNumber < 0.02 ){ //10000 70
                    return 50;
                }else if(RateNumber <= 0.03 && RateNumber >= 0.02){ 
                    return 70;
                }else if(RateNumber <= 0.04 && RateNumber > 0.02){   
                    return 80;
                }else if(RateNumber <= 0.05 && RateNumber > 0.04){
                    return 90;
                }else{
                    return 100;
                }
            },

            recommedEngine(){ //
                for (var index = 0; index < this.results.length; index++) { //
                    var element = this.results[index];
                    element.score = this.calculateHaveSold(element.count_have_sold) * 0.2   +this.calculateBelongTime(element.good_date_belong) * 0.3 + this.calculateStartMoney(element.good_money_begin) * 0.2  + this.calculateRateNumber(element.good_income_rate) * 0.3 ;//  20% 
                }
                console.log(this.results);
                
            }
		},
		mounted() {
            this.selectGoods();//
            var user = sessionStorage.getItem('user');
			if (user) {
                user = JSON.parse(user);
                this.usrID = user.userID;
            }
            // setInterval(() => { ..
            //     this.selectGoods();
            // },"10000");
        },
}
</script>

<style>
    #main{
        width: 1000px;
        margin: 0 auto;
    }
    #page{
        margin: 0 auto; 
        height: 600px;
	}
     #search{
         /* margin-left: 10px; */
         float: left;
         margin-top: 10px;
         margin-bottom: 10px;
         width: 200px;
    }
     #sort{
        float: left;
        margin-top: 10px;
        margin-left: 30px;
        margin-bottom: 10px;
    }

     .seamless-warp {
        height: 229px;
        overflow: hidden;
    }
</style>





