import Login from './views/Login.vue'
import index from './views/index.vue'
import NotFound from './views/404.vue'
import Goods from './components/good_detail.vue'
import Person from './components/person_detail.vue'
import Checkout from './components/checkout.vue'
import SellerRegister from './components/SellerRegister.vue'
import Cart from './components/cart.vue'
import OrderPage from './components/personpage/Orderpage.vue'
import info from './components/personpage/info.vue'
import Personinfo from './components/personpage/personinfo.vue'
import Resellpage from './components/personpage/resellpage.vue'
import Indexlogin from './components/index-login.vue'
import Forbidden from './views/forbidden.vue'
import ChangePwd from './views/changePasswd.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
// 
import RegisterManage from './views/nav1/RegisterManage.vue' 
import PersonManage from './views/nav1/personManage.vue'  
import AuthrityManage from './views/nav1/AuthrityManage.vue'  
// 
import OrderManage from './views/nav2/OrderManage.vue'
import ResellManage from './views/nav2/resellMange.vue'
import GoodsManage from './views/nav2/GoodsManage.vue'


import GoodUploading from './views/nav3/GoodUploading.vue'
import CreateNew from './views/nav3/CreateNew.vue'
import SellerInfo from './views/nav3/sellerInfo.vue'
import compayInfo from './views/nav3/companyInfo.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
      {
        path: '/',
        component: index,
        name: '',
        redirect: '/Goods',
        hidden: true,   //hidden 
        seller:false,
        children: [ //router
                    { path: '/Goods', component: Goods, name: '', hidden: true },
                    { path: '/info', component: info, name: '' ,hidden: true},
                    { path: '/Person', component: Person, name: '' ,redirect: '/OrderPage',
                
                        children: [  // person
                            {path: '/OrderPage', component: OrderPage, name: '',},
                            {path: '/Personinfo', component: Personinfo, name: '',},
                            {path: '/Resellpage', component: Resellpage, name: '',},
                        ]},
                    { path: '/Checkout', component: Checkout, name: '' },
                    { path: '/Cart', component: Cart, name: '' },
                    // { path: '/SellerRegister', component: SellerRegister, name: '' },
                ]
    },
    {
        path: '/login',
        component: Login,
        name: '',
        seller:false,
        hidden: true
    },
    {
        path: '/SellerRegister',
        component: SellerRegister,
        name: '',
        seller:false,
        hidden: true
    },
    {
        path: '/indexlogin',
        component: Indexlogin,
        name: '',
        seller:false,
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        seller:false,
        hidden: true
    },
    {
        path: '/forbidden',
        component: Forbidden,
        name: '',
        seller:false,
        hidden: true
    },
    { //
        path: '/home',
        component: Home,
        name: '',
        seller:false,
        iconCls: 'el-icon-message',//class
        children: [ //router
            { path: '/PersonManage', component: PersonManage, name: '' },
            { path: '/AuthrityManage', component: AuthrityManage, name: '' },
        ]
    },
    {
        path: '/home',
        component: Home,
        name: '',
        seller:false,
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/GoodsManage', component: GoodsManage, name: '' },
            { path: '/OrderManage', component: OrderManage, name: '' },
            { path: '/ResellManage', component: ResellManage, name: '' }
        ]
    },
    {
        path: '/home',
        component: Home,
        name: '',
        hidden:true,
        seller:true, //seller 
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/GoodUploading', component: GoodUploading, name: ''},
            { path: '/CreateNew', component: CreateNew, name: '' },
        ]
    }, {
        path: '/home',
        component: Home,
        name: '',
        hidden:true,
        seller:true, //seller 
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/SellerInfo', component: SellerInfo, name: '' },
            { path: '/compayInfo', component: compayInfo, name: '' },
        ]
    },
    {
        path: '*',
        hidden: true,
        seller:false,
        redirect: { path: '/index' }
    }
];

export default routes;


