<template>
 <section>
  
  <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :model="filters" :inline="true" >
          <el-form style="float:left;margin-right:10px">
          <div class="block" >
            <el-date-picker
              v-model="datetimeRange"
              type="daterange"
              align="right"
              placeholder=""
              :picker-options="Time_pickerOptions">
            </el-date-picker>
		      </div>
        </el-form>

        <el-form-item>
          <el-button type="primary" v-on:click="findSomething"></el-button>
        </el-form-item>

      </el-form>
    </el-col>

  <el-table  :data="results" highlight-current-row  v-loading="listLoading" @selection-change="selsChange" style="width: 100%" label-width="0px" >
    <el-table-column type="expand">
      <template scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
			
          <el-form-item label="">
            <span>{{ props.row.good_money_begin + '' }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_inc_money + '' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{  (props.row.good_income_rate * 100).toFixed(2) + '%' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{ props.row.good_date_begin.substring(0,10)+ '  '+ props.row.good_date_end.substring(0,10)}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_income.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_belong  + ''}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.count_have_sold + '' }}</span>
          </el-form-item>
          <el-form-item label="">
              <span>{{ props.row.good_others }}</span>
            </el-form-item>
          <el-form-item label="">
              <span>{{ props.row.goodBelongPerInfo }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.user_name }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.userPhoneNumber }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.userIDCard }}</span>
          </el-form-item>
         
          <el-form-item label="">
                <span>{{ props.row.goodsInOutDate}}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column
      type="index" width="60px" >
    </el-table-column>
    <el-table-column
      label=""
      prop="good_name"
      width="250" >
    </el-table-column>
    <el-table-column
      label="" width="120"
       >
      <template scope="scope">
       <span>{{ scope.row.goodHaveSoldNum + '' }}</span>
      </template>
    </el-table-column>
	  <el-table-column
      label=""
      width="120"
       >
      <template scope="scope">
       <span>{{ scope.row.goodSum + '' }}</span>
      </template>
    </el-table-column>

      <el-table-column label="" width="230"   prop="percentage">
        <template scope="scope">
             <el-progress :text-inside="true" :stroke-width="18" :percentage="scope.row.percentage" status="success"></el-progress>
      </template>
      </el-table-column>

     <el-table-column
      label=""
      prop="goodStatus">
      <template scope="scope">
                    <el-tag type="success" v-if="scope.row.goodStatus == 1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-else-if="scope.row.goodStatus == -1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-else-if="scope.row.goodStatus == 0"  close-transition>
                        
                    </el-tag  >
      </template>
    </el-table-column>
     <el-table-column
      label=""
      width="80">
      <template scope="scope">
        <div v-if="scope.row.goodStatus != -1" >
          <el-button  type="danger" :disabled="scope.row.goodStatus == 0" size="small" @click="handleChangeStatus(0,scope.row)"></el-button>
        </div>
      </template>
    </el-table-column>

  </el-table> 

  <!---->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
      </el-pagination>
     
    </el-col>
  </section>
</template>

<script > 
  import util from '../../common/js/util'
  import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';
  export default {
  name: 'input',      // 
  data () {
    return { 

      filters: {
          name: ''
        },
      userID: '',
      OrderDetailFormVisible: false,
      results : [],
      datetime:'',
      showResults:[],
      listLoading: false, 
      editFormVisible: false,//
      editLoading: false, 
      sels: [],// 
	    users: [], //mockjs  
	    user:'',//user
      page: 1,
      total: 0,

      datetimeRange: [],
      Time_pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },

      editFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },


    }
  },

  methods: {

    // 
    handleCurrentChange(val) {
        this.page = val;
        this.selectGoodsInfo();
        // console.log(val);
      },

       handleChangeStatus: function (status, row) { // index   index  
          console.log(status);
          console.log(row);
          var goodIncID = row.goodIncID;
          this.$http.post('./api/user/updateGoodStatus', { 
            status:    status,
            goodIncID:  goodIncID
            }, {}).then((response) => {
            this.$message({
              message: '',
              type: 'success'
            });
            this.selectGoodsInfo();
          });
    },

    selectGoodsInfo() {  //
	  var userID = this.userID;
	  console.log(userID);
      this.listLoading = true;
      this.$http.post('./api/user/selectGoodsInfoByID', {
		  page: this.page,
		  userID: userID,
          name: this.filters.name
      },{}).then((response) => {
        this.results = response.body.data;
        this.total = response.body.total;
        
        for(var index = 0; index < this.results.length; index ++ ){
            var element = this.results[index] ;
            element.goodsInOutDate = new Date(element.goodsInDate).toLocaleString();
            element.percentage = (parseInt(element.goodHaveSoldNum)  / parseInt(element.goodSum) * 100).toFixed(2);
            element.percentage = parseInt(element.percentage); //
        }
        console.log( this.results);
        this.listLoading = false;
      })
    },
    handleViewDetail(row){ // 
        var goodIncID = row.goodIncID;
        console.log(row.goodIncID);
        
         this.showResults.length = 0;
         this.$http.post('./api/user/selectOrderByGoodIncId', {
                goodIncID : goodIncID,
            },{}).then((response) => {
               this.showResults = response.body;
                console.log(response.body);
               
            })
        console.log(this.showResults);
        var date = new Date();
        this.datetime = date.toLocaleString();
        this.OrderDetailFormVisible = true;
    },


    //
    handleEdit: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
    },

     findSomething() {  //
        if (this.datetimeRange.length != 0) {
        var dateStart = this.datetimeRange[0]; //
        var dateEnd = this.datetimeRange[1]; 
    		var date;
    		var str;
    		for (var i = 0  ; i < this.results.length ;) {  //
    			str = this.results[i].goodsInDate;  //
       		// str = str.replace(/-/g,"/");
          console.log(str);
          
    			date = new Date(str);
          if (dateStart <= date  &&  dateEnd >= date) {
                i++;
                continue;
            }else{
              this.results.splice(i,1);
            }
        }
        	console.log(this.results);
        }
        if ( this.datetimeRange.length == 0 || this.results.length ==0 ) {
            this.selectGoodsInfo();
        }
    },

    //
    addSubmit: function () {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              let para = Object.assign({}, this.addForm);
              // console.log(para);
             
              para.birthday = (!para.birthday || para.birthday == '') ? '' : util.formatDate.format(new Date(para.birthday), 'yyyy-MM-dd');  //


              this.$http.post('./api/user/addUser', {   //{ } json
              username:     para.name,
              birthday:     para.birthday.substring(0,10),
              sex:          para.sex,
              id_card:      para.ID_card,
              native_place: para.native_place,
              address:      para.address,
              phone:        para.phone,
              others:       para.others,
            }, {}).then((response) => {
              this.editLoading = false;
                      //NProgress.done();
                      this.$message({
                        message: '',
                        type: 'success'
                      });
                      this.$refs['addForm'].resetFields();
                      this.addFormVisible = false;
                      this.selectGoodsInfo();
                      this.addLoading = false;
            });   });
          }
        });
      },

    //mockjs 
    mock_addusers: function () { 
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              // console.log(para);  

              let para = {
                page: this.page,
                name: this.filters.name
              };
              this.listLoading = true;
              var i;
              //NProgress.start();
              getUserListPage(para).then((res) => {
                this.total = res.data.total;
                //
                this.users = res.data.users; 
                //NProgress.done();
                // console.log(this.users[0].name); 
                 for ( i=0; i < this.users.length; i++) {
                    var _native_place = this.users[i].addr.substring(0,2); 
                    this.$http.post('./api/user/addUser', {   //{ } json
                    username:     this.users[i].name,
                    birthday:     this.users[i].birth,
                    sex:          this.users[i].sex,
                    id_card:      this.users[i].id_card,
                    native_place: _native_place,
                    address:      this.users[i].addr,
                    phone:        this.users[i].phone,
                    others:       this.users[i].other,
                  }, {}).then((response) => {});
              }
              if (i == this.users.length) {
                this.$message({
                        message: '',
                        type: 'success'
                      });
                      this.selectGoodsInfo();
                      this.addLoading = false; // 
                      this.listLoading = false;
              } 
  
                // console.log(this.total);
              });  

          });
          
       
      },
    
    selsChange: function (sels) {
        this.sels = sels;
    },

    //
    batchRemove: function () {
        var ids = this.sels.map(item => item.number_id).toString();
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          let para = { ids: ids };  //  json

          this.listLoading = false;
          this.$http.post('./api/user/batchdeleteUser', { 
            paraaaa:      para
            }, {}).then((response) => {
            this.listLoading = false;
            this.$message({
              message: '',
              type: 'success'
            });
              this.selectGoodsInfo();
          });
        }).catch(() => {

        });
      }

  },

	 mounted: function() {  //method  
        this.$nextTick(function() {
			var user = sessionStorage.getItem('user');
			if (user) {
				user = JSON.parse(user);
				this.userID = user.userID;
				this.user = user;
				this.selectGoodsInfo();
				// console.log(this.user);    
            }
		  
        })
    }
}



</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .el-form-item__label{
    width: 140px;
    height: 51px;
  }
</style>


