    <el-dialog title="" v-model="addFormVisible"  size= "tiny" :close-on-click-modal="false"  >
      <el-form :model="addForm"  :rules="addFormRules" ref="addForm"  >
        <div align = "center">
            <el-input v-model="addForm.login_name"  placeholder=""  auto-complete="off"  style="width:300px;margin-bottom:5px;margin-bottom:5px"></el-input> 
            <el-input v-model="addForm.user_pwd" type="password"  placeholder=""  auto-complete="off"  style="width:300px;margin-bottom:5px"></el-input>
            <el-input v-model="addForm.check_password"  type="password" placeholder=""  auto-complete="off"  style="width:300px;margin-bottom:5px"></el-input>
            <el-input v-model="addForm.user_name"  placeholder=""  auto-complete="off"  style="width:300px;margin-bottom:5px"></el-input>
            <el-input v-model="addForm.userIDCard"  placeholder=""  auto-complete="off"  style="width:300px;margin-bottom:5px"></el-input>
            <el-input v-model="addForm.userPhoneNumber"  placeholder=""  auto-complete="off"  style="width:300px;margin-bottom:5px"></el-input>
            <el-select v-model="addForm.moneybackCountType" placeholder=""  style="width:300px;">
            <el-option  v-for="item in options" :key="item.value"  :label="item.label"  :value="item.value">
              </el-option>
            </el-select>
