<script>
  import { requestLogin } from '../api/api';
  export default {
    data() {
      return { 

        addFormVisible: false ,//
        addLoading: false, 
        Id_card : '',
        inputIsOk: true,
        logining: false,

        isActive: true,  // 
         addFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        // 
        addForm: {
          user_name:'',  //
          userIDCard:'',//
          userPhoneNumber:'',//
          user_pwd:'', //
          moneybackCountNo:'', //
          moneybackCountType: '', //
