          ],
          checkPass: [
            { required: true, message: '', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {

      handleSubmitLogin(ev) {
        // var _this = this;
        var temp_pwd = '';
        this.$refs.ruleForm.validate((valid) => {
          // console.log(this.ruleForm.account);
          if (valid) {
            temp_pwd = this.ruleForm.checkPass;
            this.ruleForm.checkPass = hex_md5(this.ruleForm.checkPass); //md5
            // console.log(this.ruleForm.checkPass);
            this.$http.post('./api/user/buyer_login', {   //{ } json
                username: this.ruleForm.account, 
                password: this.ruleForm.checkPass
            }, {}).then((response) => {
              let { code, msg, user } = response.data;
              // console.log( JSON.stringify(user));
              if (code == 500) {
                this.$message({
                  message: msg,
                  type: 'error'
                });
                this.ruleForm.checkPass = temp_pwd;
              }else if(code == 300){
                this.$notify({
                  title: '',
                  message: msg ,
                  duration: 0
                 });

                this.ruleForm.checkPass = temp_pwd;
              } else {
                //  console.log(JSON.stringify(user));
                sessionStorage.setItem('user', JSON.stringify(user) );//session
                this.$router.push({ path: '/' });
              }
            });

          } else {
            console.log('error submit!!');
            return false;
          }

        });
      },

      handleAdd() {
          this.addFormVisible = true ;
          this.addForm = {//
            user_name:'',  //
            userIDCard:'',//
            userPhoneNumber:'',//
            user_pwd:'', //
            moneybackCountNo:'', //
            moneybackCountType: '', //
            login_name: '',     //
            check_password :'', //
            server_bank:''  //
        };
      }, 
      // 
      handleBack(){
          this.$router.push({ path: '/' });
      },
      //
     addSubmit: function () {
       var temp_pwd = '';  //
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true;
              //NProgress.start()
              if ( this.addForm.user_pwd === this.addForm.check_password  ) {
                temp_pwd = this.addForm.user_pwd;
                this.addForm.user_pwd = hex_md5(this.addForm.user_pwd); //md5
                this.addForm.check_password = this.addForm.user_pwd;
                this.addForm.user_type = 'buyer';
                this.addForm.user_status = 1 ;
                let para = {user : this.addForm };
                console.log(para);
                this.$http.post('./api/user/register', {   //{ } json
                  userInfo : para
                }, {}).then((response) => {
                      console.log(response.body.code);//200  500
                        if (response.body.code == 200) {
                             this.$message({
                            message: '',
                            type: 'error'
                            });
                            this.addForm.user_pwd = temp_pwd;
                            this.addForm.check_password = temp_pwd;
                            this.addLoading = false;

                        } else {
                            this.editLoading = false;
                            //NProgress.done();
                           this.$message({
                            message: '',
                            type: 'success'
                            });
                          this.$refs['addForm'].resetFields();
                          this.addFormVisible = false;
                          this.addLoading = false;
                          this.ruleForm.account =  this.addForm.login_name ;
                          this.ruleForm.checkPass  = '';
                        }
                    });
              } else {
                  this.$notify({
                  message: '!',
                  type: 'error',
                  title:''
                });
                this.addLoading = false;
              }
            });
          }
        });
      },
    },
    watch:{  //@change 
//      'addForm.username'(curVal,oldVal){
//     this.check_username_haved();  // username
// },
    },

  }

</script>

