<template>
 <section>
  
  <!---->
     <div> 
            <div id="search" >
                <el-input
                        placeholder=""  icon="search" v-model="search"  :on-icon-click="handleIconClick">
                </el-input> 
            </div>
             <div class="block"  style="float: left;margin-top: 10px;margin-left:10px">
                <el-select v-model="value" placeholder=""  @change="filterRate"  clearable >
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
		        </div>
            <!-- <div>
                <el-button type="primary" style="float: left;margin-top: 10px;margin-left:10px"  @click="filterRate" ></el-button>
            </div> -->
        </div>

  <el-table  :data="results" highlight-current-row  v-loading="listLoading" @selection-change="selsChange" style="width: 100%" label-width="0px" >
    <el-table-column type="expand">
      <template scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="">
            <span>{{ props.row.userIDCard }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.userPhoneNumber }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{  props.row.user_property  + '' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{ props.row.user_register_time.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.moneybackCountType }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.moneybackCountNo }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.server_bank  }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.others }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
   
    <el-table-column
      type="index" width="60px" >
    </el-table-column>
    <el-table-column
      label="ID"
      prop="userID"
      width="150" >
    </el-table-column>
    <el-table-column
      label=""
      prop="user_name"
      width="150">
    </el-table-column>
    <el-table-column
      label=""
      prop="login_name">
      <template scope="scope">
       <span>{{ scope.row.login_name}}</span>
      </template>
    </el-table-column>
      <el-table-column
      label=""
      prop="user_type">
      <template scope="scope">
                    <el-tag type="success" v-if="scope.row.user_type == 'buyer' "  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-else-if="scope.row.user_type == 'root'"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="primary" v-else-if="scope.row.user_type == 'seller'"  close-transition>
                        
                    </el-tag  >
          </template>
    </el-table-column>
     <el-table-column
      label=""
      prop="user_status">
      <template scope="scope">
                    <el-tag type="success" v-if="scope.row.user_status == 1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-else  close-transition>
                        
                    </el-tag  >
          </template>
    </el-table-column>

    <el-table-column label="" width="150">
          <template scope="scope">
            <el-button v-if="scope.row.Isseller" type="success" size="small" @click="viewCompanyInfo(scope.row)"></el-button>
        </template>
       
    </el-table-column>
    <el-table-column label="" width="150">
          <template scope="scope">
            <el-button v-if="scope.row.user_status == 0" type="success" size="small" @click="handleChangeStatus( 1,scope.row)"></el-button>
            <el-button v-else type="danger" size="small" @click="handleChangeStatus(0,scope.row)"></el-button>
        </template>
       
    </el-table-column>
  </el-table> 

  <!---->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
      </el-pagination>
     
    </el-col>

       <div>
              <!--  -->
             <el-dialog title="" v-model="GoodsDetailFormVisible"   :close-on-click-modal="false"  size="large" >
            <el-table :data="showResults"  style="width: 100%; margin-bottom:10px">
                <el-table-column  fixed  prop="companyName" label=""  width="300">
                </el-table-column>
                <el-table-column  prop="companyRegisterId" label=""  width="220">
                </el-table-column>
                <el-table-column prop="companyInstitutionalId"  label=""  width="190">
                </el-table-column>
                <el-table-column prop="comanyAddress"  label=""  width="120">
                </el-table-column>
                <el-table-column prop="companyTellNumber"  label=""  width="150">
                </el-table-column>
                <el-table-column prop="CompanyLicenseAddress"  label=""  width="150">
                </el-table-column>
                <el-table-column prop="RegisterMoneny"  label=""  width="120">
                </el-table-column>
                <el-table-column prop="LegalPersonName"  label=""  width="120">
                </el-table-column>
                <el-table-column prop="LegalPersonIDCard"  label=""  width="200">
                </el-table-column>
                <el-table-column prop="companyInstitutionalId"  label=""  width="170">
                  <template scope="scope">
                          {{scope.row.moneybackCountType + '' + scope.row.moneybackCountNo }}
                    </template>
                </el-table-column>
     
        </el-table>
                      
                <div slot="footer" class="dialog-footer" align ="center" >
                    <el-button @click.native="GoodsDetailFormVisible = false"></el-button>
                </div>
                </el-dialog>        
        </div>

  </section>
</template>

<script > 
  import util from '../../common/js/util'
  import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';
  export default {
  name: 'input',      // 
  data () {
    return { 

      filters: {
          name: ''
        },
      user_ID: '',
      results : [],
      showResults:[],
      listLoading: false, 
      editFormVisible: false,//
      editLoading: false, 
      sels: [],// 
      users: [], //mockjs  
      page: 1,
      total: 0,
      search :'', 
      GoodsDetailFormVisible:false,

      editFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },

        options: [
        { value: '1',
          label: ''
          }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }],
        value: '',

      //
        editForm: {
          number_id: 0, //
          Id_card: 0,
          name: '',
          sex:  -1,
          ID_card: '',
          birthday : '',
          native_place: '',
          phone: '',
          address: '',
          others: '',
        },

        addFormVisible: false ,//
        addLoading: false, 
        addFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        //
        addForm: {
          Id_card: 0,
          name: '',
          sex:  -1 ,
          ID_card: '',
          birthday : '',
          native_place: '',
          phone:  '',
          address: '',
          others: '',
        }
    }
  },

  methods: {

    // 
    handleCurrentChange(val) {
        this.page = val;
        this.selectGoodsInfo();
        // console.log(val);
      },

    selectGoodsInfo() {  //
      // var name = this.userName;
      this.listLoading = true;
      this.$http.post('./api/user/selectPersonInfo', {
          page: this.page,
          name: this.filters.name
      },{}).then((response) => {
        //console.log(response.body[0].username);  
         // console.log(response.body); 
        this.results = response.body.data;
        for (var index = 0; index < this.results.length; index++) {
          const element = this.results[index];
          element.Isseller = false;
          if(element.user_type == 'seller'){
              element.Isseller = true;
          }
        }
        this.total = response.body.total;
        console.log( this.results);
        this.listLoading = false;
      })
    },

    StatusOperation(UserType){
          for(var index = 0; index < this.results.length ; ){
              var element = this.results[index].user_type;

              if (element === UserType ) {
                  index ++;
                  continue;
              }else{
                  this.results.splice(index,1);
              }
          }
      },

      filterRate(){ //
          var val = this.value;
          if (this.results.length != 0 && this.value!='') {
            switch (val) {
              case '1':
                  this.StatusOperation('root'); //
                  break;
              case '2':
                  this.StatusOperation('buyer');//
                  break;
              case '3':
                  this.StatusOperation('seller'); //
                  break;
              }
          }else{
                  this.selectGoodsInfo();
          }
      },
     handleIconClick() { //
      var key = this.search;
        if ( (key  == '' || this.results.length ==0 )  ) {
                  this.selectGoodsInfo();
          }
          else{
                for (var i = 0  ; i < this.results.length ; ) {
                    if ( this.results[i].user_name.toString().search(key) != -1  ) 	  
                        {
                            i++;
                            continue;
                        }
                        else
                        {
                            this.results.splice(i,1);
                            //  length  
                        }
                      }
                  }
      },
     // 
    handleChangeStatus: function (status, row) { // index   index  
          console.log(status);
          console.log(row);
          var userID = row.userID;
          this.$http.post('./api/user/updatePersonStatus', { 
            status:    status,
            userID:    userID
            }, {}).then((response) => {
            this.$message({
              message: '',
              type: 'success'
            });
            this.selectGoodsInfo();
          });
    },

    viewCompanyInfo(row){
          this.GoodsDetailFormVisible = true;
          var companyRegisterId = row.companyRegisterId;
          this.selectCompanyInfo(companyRegisterId);
          
    },

     selectCompanyInfo(companyRegisterId) {  //
      // var name = this.userName;
      this.$http.post('./api/user/selectCompanyInfo', {
         companyRegisterId: companyRegisterId
      },{}).then((response) => {
        //  console.log(response.body[0]); 
         
        this.showResults.length = 0;
        this.showResults.push(response.body[0]);
        // console.log( this.showResults);
      })
    },


    
    selsChange: function (sels) {
        this.sels = sels;
    },

    //
    batchRemove: function () {
        var ids = this.sels.map(item => item.number_id).toString();
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          let para = { ids: ids };  //  json

          this.listLoading = false;
          this.$http.post('./api/user/batchdeleteUser', { 
            paraaaa:      para
            }, {}).then((response) => {
            this.listLoading = false;
            this.$message({
              message: '',
              type: 'success'
            });
              this.selectGoodsInfo();
          });
        }).catch(() => {

        });
      }

  },

    mounted: function() {  //method  
        this.$nextTick(function() {
          this.selectGoodsInfo();
        })
    }
}



</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .el-form-item__label{
    width: 140px;
    height: 51px;
  }
</style>


