<template>
    <div >
        <!-- div -->
        <div v-if="show" style="margin-top:20px;" >
            <h3 style="margin-left:360px;margin-bottom:10px"></h3>
             <div class="box-l">
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
            </div>
            <div class="box-m">
                <div class="text-m">{{goodinfo.orderNo}}</div>
                <div class="text-m">{{goodinfo.good_name}}</div>
                <div class="text-m">{{goodinfo.good_belonged_bank}}</div>
                <div class="text-m">{{goodinfo.good_date_belong + ''}}</div>
                <div class="text-m">{{goodinfo.goodCnt + ''}}</div>
                <div class="text-m">{{(goodinfo.good_income_rate * 100).toFixed(2) + '%'}}</div>
                <!-- <div>
                <el-button type="primary" @click="updateUserInfo"></el-button>
                </div> -->
            </div>
            
            <div class="box-l">
                <div class="text-l"> </div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
            </div>
            <div class="box-m">
                <div class="text-m">{{goodinfo.goodIncomeNum + ''}}</div>
                <div class="text-m">{{goodinfo.good_date_income.substring(0,10)}}</div>
                <div class="text-m">{{user.user_name}}</div>
                <div class="text-m">{{user.userPhoneNumber}}</div>
                <div class="text-m">{{user.moneybackCountType}}</div>
                <div class="text-m">{{user.moneybackCountNo}}</div>
            </div>
            <div class="reason">
                    <div style="float:left" >
                    
                    </div>
                    <div style="float:left;margin-left: 20px; width:400px">
                         <el-input  type="textarea" :rows="2"  placeholder="" v-model="others">
                            </el-input>
                    </div> 
            </div>
            <br>
            <div class="submit">
                <el-button type="primary" @click="InsertResellInfo"></el-button>
            </div>
           
        </div>
        <!-- div -->
        <div v-else style="margin-top:10px;">
            <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form  :inline="true" >
          <el-form style="float:left;margin-right:10px">
          <div class="block" >
            <el-date-picker
              v-model="datetimeRange"
              type="daterange"
              align="right"
              placeholder=" "
              :picker-options="Time_pickerOptions">
            </el-date-picker>
		      </div>
        </el-form>

        <el-form-item>
          <el-button type="primary" v-on:click="findSomething"></el-button>
        </el-form-item>

      </el-form>
    </el-col>
                  <el-table :data="resellResult"  border style="width: 100%">
                    <el-table-column  prop="goodIncID" label=""   width="95">
                    </el-table-column>
                    <el-table-column  prop="orderNo" label=""   width="140">
                    </el-table-column>
                    <el-table-column  prop="good_name" label=""   width="140">
                    </el-table-column>
                    <el-table-column  prop="have_bought_number" label=""  width="100">
                    </el-table-column>
                    <el-table-column  prop="applyDate" label="" width="180" > </el-table-column>
                    <el-table-column  prop="apply_reason" label=""> </el-table-column>
                    <el-table-column  prop="address" label=""> 
                        <template scope="scope">
                                    <el-tag  type="success"  v-if="scope.row.is_sold == 1 &&  scope.row.is_ok == 1 " close-transition>
                                            
                                    </el-tag>
                                    <el-tag  type="primary"  v-else-if="scope.row.is_ok == 0 " close-transition>
                                            
                                    </el-tag>
                                    <el-tag  type="success"  v-else-if="scope.row.is_ok == 1" close-transition>
                                            
                                    </el-tag>
                                    <el-tag  type="danger"  v-else-if="scope.row.is_ok == -1" close-transition>
                                            
                                    </el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column  prop="address" label=""> 
                        <template scope="scope">
                            <el-button type="primary" @click="deleteResell(scope.row)" :disabled="scope.row.is_sold == 1" ></el-button>
                        </template>
                    </el-table-column>

                </el-table>
                <div style="margin-top:30px;margin-left:20px">
                    
                    
                    1
                    2
                    3
                    4  
                </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
      return {
        resellResult: [], //
        user:'',
        show:false,
        goodinfo:'', //
        others:'',
        datetimeRange: [],
      Time_pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
      }
      }
    },
    methods:{
        InsertResellInfo(){ //
            let para = { goodinfo : this.goodinfo, user: this.user, others: this.others }; //
            console.log(para); //json  
            this.$http.post('./api/user/InsertResellInfo', {   //{ } json
                paraaaa:                para,
            }, {}).then((response) => {
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                   this.$router.push({ path: '/Personinfo' });
                   this.$router.go(-1);
            });  
        },

        findSomething() {  //
        var flag = false;
        if (this.datetimeRange.length != 0) {
          var dateStart = this.datetimeRange[0]; //
          var dateEnd = this.datetimeRange[1]; 
          var date;
          var str;
          console.log(this.datetimeRange);
          
        flag =true;
    	for (var i = 0  ; i < this.resellResult.length ;) {  //
    			str = this.resellResult[i].apply_data;  //
            date = new Date(str);
            console.log(date);
            
            if (dateStart <= date  &&  dateEnd >= date) {
                    i++;
                    continue;
                }else{
                this.resellResult.splice(i,1);
                }
        }
          this.datetimeRange.length = 0;
        }
        if ( this.datetimeRange.length == 0 && !flag ) {
            this.selectResellResult();
        }
    },
        selectResellResult(){ // 
                this.$http.post('./api/user/selectResellResult', {   //{ } json
                    userId:                this.user.userID
                }, {}).then((response) => {
                var ResellResult = response.data;
                for (var index = 0; index < ResellResult.length; index++) {
                    var element = ResellResult[index];
                    // element.apply_data = new Date(element.apply_data).toLocaleString();
                    element.applyDate = new Date(element.apply_data).toLocaleString();

                }
                console.log(ResellResult);
                 this.resellResult = ResellResult;
            }); 
        },
        deleteResell(row){ //
            console.log(row);
            var resell_id = row.resell_id;
             this.$http.post('./api/user/deleteResell', {   //{ } json
                    resell_id:                resell_id
                }, {}).then((response) => {
                      this.$message({
                        message: '',
                        type: 'success'
                    });
                var ResellResult = response.data;
                console.log(ResellResult);
                this.selectResellResult();
            }); 
        }
    },
     mounted() {
            var user = sessionStorage.getItem('user');
            var goodinfo = sessionStorage.getItem('resellGoodsInfo');
            if(goodinfo){ //
                this.show = true;
                sessionStorage.removeItem('resellGoodsInfo');
            } 
            if (user) {
                this.goodinfo = JSON.parse(goodinfo);
                this.user = JSON.parse(user); 
                console.log(this.user);
                this.selectResellResult();
        }
     }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
    .text-l{
       margin-bottom: 30px;
       text-align:right;
       font-family: Microsoft YaHei;
       font-size: 15px;
   }
   .box-l{
    //    border-right: 1px solid #ccc;
       width:160px;
       float: left;
   }
   .box-m{
    //    border-right: 1px solid #ccc;
       width:300px;
       float: left;
       margin-left: 20px;
   }

    .text-m{
       margin-bottom: 30px;
       text-align:left;
       font-family: Microsoft YaHei;
       font-size: 15px;
   }
   .reason{
       margin-left: 159px;
   }

   .submit{
        margin-left: 30px;
        float: left;

   }
</style>




