<template>
<div style="margin-top:10px">
  <div v-show="info">

    <div  class="bg-purple-light">
        <span style="font-size: 20px;margin-left:20px;" >   ,{{user.user_name}} </span>
    </div>

    <div class="bg-purple">
        <div class="text-r-out-in" style="margin-bottom:10px"></div>
        <el-row type="flex" >
                <el-col :span="6"><div class="grid-content ">
                    <span style="color:#13CE66 ; margin-left:10px">{{sum}} </span>
                    </div></el-col>
                <el-col :span="6"><div class="grid-content ">
                    <span style="color:#20A0FF">{{payed}} </span> <div style="float:right">  </div>
                </div></el-col>
                <el-col :span="12"><div class="grid-content"  >
                 <el-progress   :text-inside="true" :stroke-width="18" :percentage="percentage"></el-progress>
                </div></el-col>
                   
        </el-row>  
          
    </div>
    
    <div class="bg-purple-light">
        <div class="text-r-out-in" style="margin-bottom:10px" ></div>
        <div style="color:red"></div>
        <el-row type="flex" >
            <el-col :span="8"><div class="grid-content ">
                {{user.login_name}}
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                {{user.userPhoneNumber}}
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                {{user.userIDCard}}
                </div></el-col>
        </el-row>
        <el-row type="flex" >
            <el-col :span="8"><div class="grid-content ">
            {{user.moneybackCountType}} {{user.moneybackCountNo}}
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                {{user.user_register_time}}
                </div></el-col>
         
        </el-row>
        <div style="color:red"></div>
         <el-row type="flex" >
            <el-col :span="8"><div class="grid-content ">
            {{CompanyInfo.companyName}} 
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                {{CompanyInfo.companyRegisterId}}
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                {{CompanyInfo.companyInstitutionalId}}
            </div></el-col>
        </el-row>
         <el-row type="flex" >
            <el-col :span="8"><div class="grid-content ">
            {{CompanyInfo.comanyAddress}} 
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                {{CompanyInfo.companyTellNumber}}
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                :{{CompanyInfo.CompanyLicenseAddress}} 
            </div></el-col>
        </el-row>
        <el-row type="flex" >
            <el-col :span="8"><div class="grid-content ">
            {{CompanyInfo.RegisterMoneny}}
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                {{CompanyInfo.LegalPersonName}}
                </div></el-col>
            <el-col :span="8"><div class="grid-content ">
                 {{CompanyInfo.LegalPersonIDCard}} 
            </div></el-col>
        </el-row>
    </div>
    
</div>
    
</div>
    
</template>

<script>
export default {
    data() {
      return {
        OrderResults: [], //
        user:'',
        info:true,
        alert:false,
        payed:0,//
        sum:0, //
        goodIncomeNum:0, //
        goodsunResellNum:0,//
        MoneySum:'', //
        freezeMoney:'', //
        userPhoneNumber:'',
        userMoneyBackcount:'',
        userId:'',  //id
        payTab :'',
        CompanyInfo:'',//
        percentage:0,

      }
    },
    methods:{
    
    selectGoodsInfo(userID) {  //
	    // var userID = this.userID;
	    console.log(userID);
      this.$http.post('./api/user/selectGoodSumInfoByID', {
		  userID: userID,
      },{}).then((response) => {
        var results = response.data
        console.log(results);
        var sum = 0;
        var payed  = 0;
        for (var  index = 0; index < results.length; index++) {
            var  element = results[index];
            sum = sum + element.goodSum;
            payed = payed + element.goodHaveSoldNum;
        }
        this.sum = sum;
        this.payed = payed;
        this.percentage =  (parseInt(payed) / parseInt(sum) * 100).toFixed(2);
        this.percentage =  parseInt( this.percentage) ;
      })
    },

        updateUserProperty(){//  
            this.$http.post('./api/user/updateUser_property', {
                userID : this.userId,
                User_property: this.MoneySum
            },{}).then((response) => {})
        },


        selectCompanyInfo(){ //
                this.$http.post('./api/user/selectCompanyInfo', {   //{ } json
                 companyRegisterId:                this.user.companyRegisterId
                }, {}).then((response) => {
                this.CompanyInfo = response.data[0];
                
            }); 
        },
    

    },
     mounted() {
            var user = sessionStorage.getItem('user');
            if (user) {
            this.user = JSON.parse(user);
            // console.log(this.user);
            this.userId = this.user.userID;
            // console.log(this.userId);
            this.selectGoodsInfo(this.userId);
            this.selectCompanyInfo();
        }
     }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
   .text-l{
       margin-bottom: 30px;
       text-align:right;
       font-family: Microsoft YaHei;
       font-size: 20px;
   }
   .box-l{
    //    border-left: 1px solid #ccc;
    //    border-top: 1px solid #ccc;
    //    border-bottom: 1px solid #ccc;
       width:120px;
       float: left;
   }
   .box-m{
    //    border-right: 1px solid #ccc;
    //    border-right: 1px solid #ccc;
    //    border-top: 1px solid #ccc;
    //    border-bottom: 1px solid #ccc;
       width:250px;
       float: left;
    //    margin-left: 20px;
   }

    .text-m{
       margin-bottom: 30px;
       text-align:left;
       font-family: Microsoft YaHei;
       font-size: 20px;
   }

   .inputStyle{
        margin-bottom: 20px;
   }

   .box-r{
        margin-left: 60px;
        width: 100%;
   }

   .text-r-moneySum{
       font-family: Microsoft YaHei;
       font-size: 20px;
       padding-left: 30px;
   }
   .text-r-out-in{
        font-family: Microsoft YaHei;
        font-size: 20px;
        margin-top: 20px;
   }

     .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
    padding: 20px;
  }
  .bg-purple-light {
    background: #e5e9f2;
    padding: 20px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>




