<template>
  <div>
      <!--  -->
      <div v-show="orderDetail">
    <!---->
    <el-col :span="24" class="toolbar" >
      <el-form  :inline="true" >

        <el-form style="float:left;margin-right:10px">
          <el-input
                    placeholder=""  icon="search" v-model="search"  :on-icon-click="handleIconClick">
                </el-input> 
        </el-form>

        <el-form style="float:left;margin-right:10px">
          <div class="block" >
            <el-date-picker
              v-model="datetimeRange"
              type="daterange"
              align="right"
              placeholder=""
              :picker-options="Time_pickerOptions">
            </el-date-picker>
		      </div>
        </el-form>

         <el-form style="float:left;margin-right:10px">
             <el-button type="primary" v-on:click="findSomething"></el-button>
        </el-form>
         <el-form style="float:left;margin-right:10px">
           <el-select v-model="Orderstatus" placeholder=""   @change="OrderChange"  clearable style="width:130px" >
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
          </el-form> 
      </el-form>

    </el-col>
    
      <el-table  :data="results" highlight-current-row  v-loading="listLoading" @selection-change="selsChange" style="width: 100%" label-width="0px" >
    <el-table-column type="expand">
      <template scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item 
            label=" ">
             <span>{{ props.row.orderNo }}</span>
            </el-form-item >
          <el-form-item label="">
            <span>{{ props.row.good_date_end.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_belong + '' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{  (props.row.good_income_rate * 100).toFixed(2) + '%' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{ props.row.goodIncomeNum + '' }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_income.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_belonged_bank}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{  props.row.moneybackCountType + ' ' +  props.row.moneybackCountNo }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.userPhoneNumber }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>

            <el-table-column type="index" width="40px" >
            </el-table-column>
            <el-table-column
            label=""
            prop="orderCreateTime"
            width="110" >
            <template scope="scope">
                {{scope.row.orderCreateTime.substring(0,10)}}
            </template>
            </el-table-column>
            <el-table-column
            label=" "
            prop="good_name"
            width="200">
            </el-table-column>
            <el-table-column
            label=""
             width="90"
            prop="user_name">
            </el-table-column>
            <el-table-column
            label=""
             width="100"
            prop="goodCnt">
            <template scope="scope">
                {{scope.row.goodCnt+ ''}}
            </template>
            </el-table-column>
            <el-table-column
            label=""
            prop="goodIncomeNum"
            width="100" 
            >
            <template scope="scope">
                {{scope.row.goodIncomeNum + ''}}
            </template>
            </el-table-column>
              <el-table-column
      label=""
       width="100"
      prop="goodCnt">
        <template scope="scope">
          <el-button v-if="scope.row.resellOrderID != null"  type="success" size="small" @click="handleFindResleOrder(scope.row)"></el-button>
        </template>
    </el-table-column>
            <el-table-column
            label=""
            prop="orderStatus">
            <template scope="scope">
                    <el-tag type="success" v-if="scope.row.orderStatus == 1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-else  close-transition>
                        
                    </el-tag  >
                    <el-tag type="gray" v-if="scope.row.is_resell==1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="primary" v-if="scope.row.have_resell==1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-if="scope.row.have_resell==2"  close-transition>
                        
                    </el-tag  >
                </template>
            </el-table-column>
    <el-table-column label="" width="110">
        <template scope="scope">
          <!--  -->
          <div v-show="scope.row.have_resell == 0">
            <div v-if="scope.row.haveFinshed == 1">
                        <el-button type="success"  :disabled=" scope.row.orderStatus==0 "  size="small"  @click="handleRepay(scope.row)"></el-button> 
            </div>
            <div v-else-if= "scope.row.haveFinshed == 2 ">
                <el-tag type="success"  close-transition>
                    
                </el-tag  >   
            </div>
            <div v-else>
                  <el-popover trigger="hover" placement="top">
                    <p>{{'' + scope.row.good_date_income.substring(0,10)}}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag></el-tag>
                    </div>
                  </el-popover>
            </div>
          </div>
            
        </template>
    </el-table-column>
  </el-table> 
    <div style="margin-top:30px;margin-left:20px;color:red">
                    
                    
                    1
                    2
                    3
                    4
      </div>
  </div>
  <!--  -->
    <div v-show="repayShow">
              <!--  -->
        <div id="back">
          <span id="tittle" ></span>
          </div>
          <div id="OrderSuccess">
              <div>
                  <div id="tipsShow">
              <el-table :data="resultsRepayShow"  style="width: 98%;margin-left:10px">
                <el-table-column  fixed  prop="good_name" label=""  width="240">
                </el-table-column>
                <el-table-column prop="good_date_belong"  label=""  width="130">
                    <template scope="scope">
                          {{scope.row.good_date_belong + ''}}
                    </template>
                </el-table-column>
                <el-table-column  label="" width="120">
                  <template scope="scope">
                      {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
                  </template>
                </el-table-column>
                
                <el-table-column  prop="goodCnt"  label="" width="130">
                  <template scope="scope">
                      {{scope.row.goodCnt + ''}}
                  </template>
                </el-table-column>
                <el-table-column  prop="sumIncome"  label=""  width="140">
                  <template scope="scope">
                      {{scope.row.goodIncomeNum + ''}}
                  </template>
                </el-table-column>
                <el-table-column  prop="sumIncome"  label=""  width="140">
                  <template scope="scope">
                      {{scope.row.goodIncomeNum + scope.row.goodCnt + ''}}
                  </template>
                </el-table-column>
                <el-table-column  prop="sumIncome"  label=""  width="140">
                  <template scope="scope">
                      {{scope.row.user_name }}
                  </template>
                </el-table-column>
                <el-table-column  prop="sumIncome"  label=""  width="140">
                  <template scope="scope">
                      {{scope.row.moneybackCountType + scope.row.moneybackCountNo}}
                  </template>
                </el-table-column>
                <el-table-column label=""  width="160">
                <template scope="scope">
                    {{scope.row.good_date_end.substring(0,10)}}
                </template>
              </el-table-column>
          <el-table-column  prop="good_date_income"  label=""  width="150">
              <template scope="scope">
              {{scope.row.good_date_income.substring(0,10)}}
          </template>
          </el-table-column>
        </el-table>
                  </div>
                  <div class="pay-type">
                      <div class="p-title"></div>
                      <div class="pay-item">
                        <div :class="{active:payType==1}" @click="payType=1"><img src="/static/images/alipay@2x.png" alt=""></div>
                        <div :class="{active:payType==2}" @click="payType=2"><img src="/static/images/weixinpay@2x.png" alt=""></div>
                      </div>
                  </div>
              </div>
          </div>
          <div>
                <span style="padding-left:20px" >:
                      <span style="color:red">
                          {{'' + this.PaySum +''}}
                      </span>
                    </span>
                <el-button type="success"  @click="Order_success_next" style="margin-left:10px"></el-button> 
          </div>
      </div>
      <!--  -->
        <div v-show="PaySuccessShow">
            <div id="back">
                <span id="tittle" ></span>
            </div>
            <div  id="OrderSuccess">
              <div style="text-align: center">
                <p class="content"> <span class="price"><em style="font-size: 18px"></em>{{PaySum}}</span> </p>
                    <el-button type="primary" size="large" @click="returnShow" ></el-button>
              </div>
            </div>
      </div>

       <!--  -->
             <el-dialog title="" v-model="OrderDetailFormVisible"   :close-on-click-modal="false"  size="large" >
              <h3></h3>
            <el-table :data="showResults"  style="width: 100%; margin-bottom:10px">
          <el-table-column  fixed  prop="orderNo" label=""  width="220">
          </el-table-column>
          <el-table-column prop="good_name"  label=""  width="250">
              <template scope="scope">
                    {{scope.row.good_name}}
              </template>
          </el-table-column>
           <el-table-column prop="user_name"  label=""  width="120">
              <template scope="scope">
                    {{scope.row.user_name}}
              </template>
          </el-table-column>
           <el-table-column prop="good_date_belong"  label=""  width="120">
              <template scope="scope">
                    {{scope.row.good_date_belong + ''}}
              </template>
          </el-table-column>
          <el-table-column  label="" width="120">
            <template scope="scope">
                {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
            </template>
          </el-table-column>
          <el-table-column  prop="goodCnt"  label="" width="120">
            <template scope="scope">
                {{scope.row.goodCnt + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="sumIncome"  label=""  width="120">
            <template scope="scope">
                {{scope.row.goodIncomeNum + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_end" label=""  width="150">
            <template scope="scope">
                {{scope.row.good_date_end.substring(0,10)}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_income"  label=""  width="120">
              <template scope="scope">
                {{scope.row.good_date_income.substring(0,10)}}
            </template>
          </el-table-column>
          <el-table-column label=""  width="120">
              <template scope="scope">
                {{scope.row.orderCreateTime.substring(0,10)}}
            </template>
          </el-table-column>

        </el-table>
    
        <div slot="footer" class="dialog-footer" align ="center" >
            <el-button @click.native="OrderDetailFormVisible = false"></el-button>
        </div>

      
 </el-dialog> 
  </div>
</template>

<script > 
  export default {
  name: 'input',      // 
  data () {
    return { 
      userID: '',
      datetime:'',
      payType: 1,
      PaySum:'',
      results:[],
      OrderID:'', //
      resultsRepayShow:[],//
      listLoading: false, 
      editFormVisible: false,//
      filters: {  //
        name: ''
      },
      search :'', 
      repayShow:false, //
      orderDetail:true, //
      OrderDetailFormVisible:false,
      dateTime:'',//
      PaySuccessShow:false, //
      datetimeRange: [],
      showResults:[],
      Orderstatus:'',
       options: [{
                    value: 3,
                    label: ''
                    }, {
                    value: 1,
                    label: ''
                    }, {
                    value: 2,
                    label: ''
                    }],
      Time_pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
    }
  },

  methods: {

    selectOrderInfo() {  //id
      this.$http.post('./api/user/selectOrderByUserId', {
          userID : this.userID,
      },{}).then((response) => {
        this.results = response.body;
        console.log(this.results);
        var dateNow = new Date().getTime(); //
         for (var  index = 0; index < this.results.length; index++) {
              var element = this.results[index];
              var DateOrder = new Date(element.good_date_income).getTime();
              if(DateOrder < dateNow ){  //
                   if(element.haveFinshed == 0){
                        this.updateHaveFinish(1,element.orderID);
                    }
              }
           }
      })
    },

       // 
    handleFindResleOrder: function (row) { // index   index  
        
          var resellOrderID = row.resellOrderID;//ID
          console.log(resellOrderID);
          this.showResults.length = 0;
          for(var index = 0;index < this.results.length; index++ ){
            var element = this.results[index];
            console.log(element.orderID);
            if(element.orderID === resellOrderID ){
                this.showResults.push(element);
                this.OrderDetailFormVisible = true;
                break;
            }
          }
    },

     OrderChange(){ //
                // this.filterRate();
                console.log(this.Orderstatus);
                var val = this.Orderstatus;
                if (this.results.length != 0 && val !='') {
                  if(val == 3)
                      val = 0;
                  this.StatusOperation(val);
                }else{
                    this.selectOrderInfo();
                    this.Orderstatus='';
                }
            },

    
     StatusOperation(status){  //
            // console.log(status);
        for(var index = 0; index < this.results.length ; ){
            var element = this.results[index].haveFinshed;
            console.log(element);
            if (element  == status) {
                index ++;
                continue;
            }else{
                this.results.splice(index,1);
            }
        }
       },


    updateHaveFinish(status,orderID){  //  1  2 
        this.$http.post('./api/user/updateHaveFinish', {
                orderID : orderID,
                status: status,
            },{}).then((response) => {
              this.selectOrderInfo(); //
              console.log("111");
              
            })
      },

    selsChange: function (sels) {
        this.sels = sels;
    },
     
     Order_success_next(){ //
        this.repayShow = false;
        this.PaySuccessShow = true;
        this.updateHaveFinish(2,this.orderID);
     },
//  
    handleRepay: function (row) { // index   index  
        console.log(row);
        this.resultsRepayShow.length = 0;
        this.PaySum = row.goodCnt + row.goodIncomeNum;
        this.orderID = row.orderID; //
        this.resultsRepayShow.push(row);
        this.orderDetail = false;
        this.repayShow = true;
        console.log(this.resultsRepayShow);
        this.dateTime = new Date().toLocaleString();
    },

    returnShow(){ //
      this.repayShow =false; //
      this.orderDetail =true; //
      this.PaySuccessShow =false; //
      this.selectOrderInfo();
    },

    findSomething() {  //
        var flag = false;
        if (this.datetimeRange.length != 0) {
        var dateStart = this.datetimeRange[0]; //
        var dateEnd = this.datetimeRange[1]; 
    		var date;
        var str;
        flag =true;
    		for (var i = 0  ; i < this.results.length ;) {  //
    			str = this.results[i].orderCreateTime;  //
       		// str = str.replace(/-/g,"/");
          console.log(str);
          
    			date = new Date(str);
          if (dateStart <= date  &&  dateEnd >= date) {
                i++;
                      continue;
            }else{
              this.results.splice(i,1);
            }
        }
           this.datetimeRange.length = 0;
        }

        if ( this.datetimeRange.length == 0 && !flag  ) {
            this.selectOrderInfo();
        }
    
    },

      handleIconClick() { //
      console.log(this.search);
      var key = this.search;
        if ( (key  == '' || this.results.length ==0 )  ) {
                  this.selectOrderInfo();
          }
          else{
                for (var i = 0  ; i < this.results.length ; ) {
                    if ( this.results[i].good_name.toString().search(key) != -1 || this.results[i].user_name.toString().search(key) != -1 ) 	  
                        {
                            i++;
                            continue;
                        }
                        else
                        {
                            this.results.splice(i,1);
                            //  length  
                        }
                      }
                  }
  },


  

  },

	 mounted: function() {  //method  
        this.$nextTick(function() {
			var user = sessionStorage.getItem('user');
			if (user) {
				user = JSON.parse(user);
				this.userID = user.userID;
				this.user = user;
				this.selectOrderInfo();
				// console.log(this.user);    
            }
		  
        })
    }
}

</script>

    
<style lang="scss" scoped rel="stylesheet/scss">
   
    #tittle{
    font-size: 18px;
    font-weight: 400;
    color: #626262;
    display: inline-block;
    }
    #back{   //
    height: 60px;
    z-index: 10;
    line-height: 60px;
    font-size: 18px;
    color: #333;
    display: flex;
    justify-content: space-between;
    padding: 0 10px 0 24px;
    align-items: center;
    border-bottom: 1px solid #d4d4d4;
    background: linear-gradient(#fbfbfb, #ececec);
    background: -webkit-linear-gradient(#fbfbfb, #ececec);
    }

    #OrderSuccess{  // 
    height: 250px;
    border-bottom: 1px solid #d4d4d4;
    border-left: 1px solid #d4d4d4;
    border-right: 1px solid #d4d4d4;
    padding: 40px 0px 0px 0px;
    margin-bottom: 30px;
    }
    /**/
    .pay-type {
      margin: 0 auto;
      width: 95%;
      padding-bottom: 30px;
      .p-title {
        font-size: 18px;
        line-height: 40px;
        padding: 0 10px;
        position: relative;
        &:before {
          content: ' ';
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          border-bottom: 1px solid #ccc;
        }
      }

    }

    .pay-type {
      .pay-item {
        display: flex;
        align-items: center;
        div {
          margin-top: 20px;
          width: 175px;
          height: 50px;
          display: flex;
          align-items: center;
          justify-content: center;
          border: 1px solid #E5E5E5;
          cursor: pointer;
          border-radius: 6px;
          margin-right: 10px;
          background: #FAFAFA;
          &.active {
            border-color: #6A8FE5;
            background: #fff;
          }
          img {
            display: block;
            height: 34px;
            margin: 8px auto;
          }
        }
      }
    }

    #tipsShow{
      // margin-left: 300px;
      text-align: center;
      margin-bottom: 20px;
    }
    
    .payment-detail {
      text-align: center;
      line-height: 24px;
      font-size: 14px;
      color: #999;
    }

     span.price {
    color: #d44d44;
    font-weight: 700;
    line-height: 20px;
    text-align: right;
  }

  .content {
    padding: 10px 0;
    font-size: 30px;
  }
</style>



