<template>
 <section>
  
  <!---->
     <div> 
            <div id="search" >
                <el-input
                        placeholder=""  icon="search" v-model="search"  :on-icon-click="handleIconClick">
                </el-input> 
            </div>

            
        </div>
    
      <el-table  :data="results" highlight-current-row  v-loading="listLoading" @selection-change="selsChange" style="width: 100%" label-width="0px" >
    <el-table-column type="expand">
      <template scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="">
            <span>{{ props.row.good_date_end.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_belong + '' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{  (props.row.good_income_rate * 100).toFixed(2) + '%' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{ props.row.goodIncomeNum + '' }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_income.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_belonged_bank}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{  props.row.moneybackCountType + ' ' +  props.row.moneybackCountNo }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.userPhoneNumber }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
  
    <el-table-column
      type="index" width="60px" >
    </el-table-column>
    <el-table-column
      label=""
      prop="orderNo"
      width="140" >
    </el-table-column>
    <el-table-column
      label=" "
      prop="good_name"
      width="250">
    </el-table-column>
    <el-table-column
      label=""
      prop="user_name">
    </el-table-column>
    <el-table-column
      label=""
      prop="goodCnt">
    </el-table-column>
    <el-table-column
      label=""
       width="150"
      prop="orderStatus">
      <template scope="scope">
                    <el-tag type="success" v-if="scope.row.orderStatus == 1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-else  close-transition>
                        
                    </el-tag  >
                    <el-tag type="gray" v-if="scope.row.is_resell==1"  close-transition>
                        
                    </el-tag>
                    <el-tag type="primary" v-if="scope.row.have_resell==1"  close-transition>
                        
                    </el-tag>
                    <el-tag type="danger" v-if="scope.row.have_resell==2"  close-transition>
                        
                    </el-tag>
      </template>
    </el-table-column>
     <el-table-column
      label=""
      prop="goodCnt">
        <template scope="scope">
          <el-button v-if="scope.row.resellOrderID != null"  type="success" size="small" @click="handleFindResleOrder(scope.row)"></el-button>
        </template>
    </el-table-column>
    <el-table-column label="" width="100">
        <template scope="scope">
          <el-button :disabled="scope.row.haveFinshed == 2"  v-if="scope.row.orderStatus == 0 " type="success" size="small" @click="handleChangeStatus( 1,scope.row)"></el-button>
          <el-button  :disabled="scope.row.haveFinshed == 2" v-else type="danger" size="small" @click="handleChangeStatus(0,scope.row)"></el-button>
        </template>
      </el-table-column>
  </el-table> 

      <!--  -->
             <el-dialog title="" v-model="OrderDetailFormVisible"   :close-on-click-modal="false"  size="large" >
              <h3></h3>
            <el-table :data="showResults"  style="width: 100%; margin-bottom:10px">
          <el-table-column  fixed  prop="orderNo" label=""  width="220">
          </el-table-column>
          <el-table-column prop="good_name"  label=""  width="250">
              <template scope="scope">
                    {{scope.row.good_name}}
              </template>
          </el-table-column>
           <el-table-column prop="user_name"  label=""  width="120">
              <template scope="scope">
                    {{scope.row.user_name}}
              </template>
          </el-table-column>
           <el-table-column prop="good_date_belong"  label=""  width="120">
              <template scope="scope">
                    {{scope.row.good_date_belong + ''}}
              </template>
          </el-table-column>
          <el-table-column  label="" width="120">
            <template scope="scope">
                {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
            </template>
          </el-table-column>
          <el-table-column  prop="goodCnt"  label="" width="120">
            <template scope="scope">
                {{scope.row.goodCnt + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="sumIncome"  label=""  width="120">
            <template scope="scope">
                {{scope.row.goodIncomeNum + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_end" label=""  width="150">
            <template scope="scope">
                {{scope.row.good_date_end.substring(0,10)}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_income"  label=""  width="120">
              <template scope="scope">
                {{scope.row.good_date_income.substring(0,10)}}
            </template>
          </el-table-column>
          <el-table-column label=""  width="120">
              <template scope="scope">
                {{scope.row.orderCreateTime.substring(0,10)}}
            </template>
          </el-table-column>

        </el-table>
    
        <div slot="footer" class="dialog-footer" align ="center" >
            <el-button @click.native="OrderDetailFormVisible = false"></el-button>
        </div>

      
 </el-dialog> 
      <div style="margin-top:30px;margin-left:20px;color:red">
                    
                    
                    1
                    2
                    3
                    4
                    5
      </div>

  </section>
</template>

<script > 
  export default {
  name: 'input',      // 
  data () {
    return { 

      isUsed : true,
      checkBoxValue: [], // 
      listLoading: false, 
      u_name :'',
      filters: {  //
          name: ''
        },
      results : [],  //
      finds: [] ,  // 
      sels: [],// 
      page: 1,
      total: 0,
      search :'', 
      OrderDetailFormVisible:false,
      showResults:[],
    }
  },

  methods: {
    selectOrderInfo() {   //
      // var name = this.userName;
      this.listLoading = true;
      this.$http.post('./api/user/selectOrderInfo', {
          u_name : this.u_name,
      },{}).then((response) => {
        this.results = response.body;
        console.log(this.results);
        this.listLoading = false;
      })
    },

    // 
    handleChangeStatus: function (status, row) { // index   index  
          var orderID = row.orderID;
          this.$http.post('./api/user/updateOrderStatus', { 
            status:    status,
            orderID:  orderID
            }, {}).then((response) => {
            this.listLoading = false;
            this.$message({
              message: '',
              type: 'success'
            });
            this.selectOrderInfo();
          });
    },

     // 
    handleFindResleOrder: function (row) { // index   index  
        
          var resellOrderID = row.resellOrderID;//ID
          console.log(resellOrderID);
          this.showResults.length = 0;
          for(var index = 0;index < this.results.length; index++ ){
            var element = this.results[index];
            console.log(element.orderID);
            if(element.orderID === resellOrderID ){
                this.showResults.push(element);
                this.OrderDetailFormVisible = true;
                break;
            }
          }
    },

    handleIconClick() { //
      var key = this.search;
        if ( (key  == '' || this.results.length ==0 )  ) {
                  this.selectOrderInfo();
          }
          else{
                for (var i = 0  ; i < this.results.length ; ) {
                    if ( this.results[i].good_name.toString().search(key) != -1 || this.results[i].user_name.toString().search(key) != -1 ) 	  
                        {
                            i++;
                            continue;
                        }
                        else
                        {
                            this.results.splice(i,1);
                            //  length  
                        }
                      }
                  }
      },
    
    selsChange: function (sels) {
        this.sels = sels;
    }
 
  },
    mounted: function() {  //method     
      this.$nextTick(function() {
         var user = sessionStorage.getItem('user');
         if (user) {
          user = JSON.parse(user);
          this.u_name = user.u_user || '';
          };
          this.selectOrderInfo();
        })
    }
}
</script>

<style>
</style>


