<template>
<div id="container">
  <div id="header"> 
              <div id="name" align="center">
                    <h2></h2>
                </div>  
        </div>  
    <div style="width:600px;margin-top:20px;margin-left:270px">
    
        <el-form ref="form" :model="form"    :rules="rules"  label-width="130px">
          <div style="margin-left:130px;color:red">
                
              </div>
             <el-form-item label=""  prop="user_type" >
               <el-select v-model="form.user_type" placeholder=""  style="width:300px;">
                <el-option  v-for="item in userTypeOptions" :key="item.value"  :label="item.label"  :value="item.value">
              </el-option>
            </el-select>
            </el-form-item>
            <!--  -->
            <div v-show="personInfo">
              <div style="margin-left:130px">
                
              </div>
              <el-form-item label="" prop="login_name">
                      <el-input v-model="form.login_name" style="width:300px" ></el-input>
                    </el-form-item>
                    <el-form-item label=""  prop="user_pwd" >
                      <el-input  type="password"  style="width:300px" v-model="form.user_pwd"></el-input>    
                    </el-form-item>

                    <el-form-item label="" prop="check_password" >
                      <el-input  type="password"  style="width:300px" v-model="form.check_password"></el-input>      
                    </el-form-item>
                    <el-form-item label=""  prop="user_name">
                      <el-input style="width:300px" v-model="form.user_name"></el-input>      
                    </el-form-item>

                    <el-form-item label="" prop="userIDCard" >
                      <el-input style="width:300px" v-model="form.userIDCard"></el-input>      
                    </el-form-item>
                    <el-form-item label="" prop="userPhoneNumber" >
                      <el-input style="width:300px" v-model="form.userPhoneNumber"></el-input>     
                    </el-form-item>
                     </div>
            </el-form>
            <!--  -->
            <el-form ref="formCompany" :model="formCompany"   label-width="130px">
            <div v-show="companyInfo">
              <div style="margin-left:130px">
              
            </div>
            
              <el-form-item label="" prop="companyName">
                <el-input v-model="form.companyName" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="companyRegisterId">
                <el-input v-model="form.companyRegisterId" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="companyInstitutionalId">
                <el-input v-model="form.companyInstitutionalId" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="comanyAddress">
                <el-input v-model="form.comanyAddress" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="companyTellNumber">
                <el-input v-model="form.companyTellNumber" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="CompanyLicenseAddress">
                <el-input v-model="form.CompanyLicenseAddress" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="RegisterMoneny">
                <el-input v-model="form.RegisterMoneny" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="LegalPersonName">
                <el-input v-model="form.LegalPersonName" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="LegalPersonIDCard">
                <el-input v-model="form.LegalPersonIDCard" style="width:300px" ></el-input>
              </el-form-item>
              <el-form-item label="" prop="moneybackCountType" >
                        <el-select v-model="form.moneybackCountType" placeholder=""  style="width:300px;">
                    <el-option  v-for="item in options" :key="item.value"  :label="item.label"  :value="item.value">
                      </el-option>
                    </el-select>
                    </el-form-item>
                  
                    <el-form-item label="" prop="moneybackCountNo" >
                      <el-input style="width:300px" v-model="form.moneybackCountNo"></el-input>      
                    </el-form-item>
            </div>

            <el-form-item >
              <div v-show="form.user_type == 'seller'" style="float:left">
                  <el-button  v-show="companyInfo" style="margin-left:80px" type="success" @click="personInfo=true;companyInfo=false" ></el-button>
                  <el-button  v-show="personInfo" style="margin-left:80px" type="success" @click="personInfo=false;companyInfo=true" ></el-button>
              </div>
              <div>
                <el-button v-if="form.user_type == 'root'"  style="margin-left:20px" type="success" @click="RootAddSubmit" ></el-button>
                <el-button v-else  style="margin-left:20px" type="success" @click="SellerAddSubmit" ></el-button>
              </div>
                
            </el-form-item>
          </el-form>
    </div>
</div>    
</template>

<script>
export default {
     data() {
      return {
        form: {
          user_name:'',  //
          userIDCard:'',//
          userPhoneNumber:'',//
          user_pwd:'', //
          moneybackCountNo:'', //
          server_bank: '', //
          login_name: '',     //
          check_password :'', //
          moneybackCountType:'',

        },
        formCompany:{
          companyName:'', //
          companyRegisterId:'', //
          companyInstitutionalId:'', //
          comanyAddress:'', //
          companyTellNumber:'', //
          CompanyLicenseAddress:'', //
          RegisterMoneny:'', //
          LegalPersonIDCard:'', //
          LegalPersonName:'', //
          moneybackCountType:'',//
          moneybackCountNo:'',//
        },
        personInfo:true,
        companyInfo:false,
        userTypeOptions: [{ //
          value: 'seller',
          label: ''
        }, {
          value: 'root',
          label: ''
        }],
        user:'root',
        active:1,
          options: [{ //
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }],
        rules: {
          login_name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          user_pwd: [
            { required: true, message: '', trigger: 'blur' }
          ],
          check_password: [
            { required: true, message: '', trigger: 'blur' }
          ],
          user_name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          userIDCard: [
            {  required: true, message: '', trigger: 'blur' }
          ],
          userPhoneNumber: [
            {  required: true, message: '', trigger: 'blur' }
          ],
          server_bank: [
            {  required: true, message: '', trigger: 'blur' }
          ],
          moneybackCountNo: [
            {  required: true, message: '', trigger: 'blur' }
          ],

        },
      }
    },
    methods: {

    //
     RootAddSubmit: function () {
       console.log(this.form);
    this.form.server_bank = this.formCompany.companyName;
     var temp_pwd = '';  //
        if ( this.form.user_pwd === this.form.check_password  ) {
          temp_pwd = this.form.user_pwd;
          this.form.user_pwd = hex_md5(this.form.user_pwd); //md5
          this.form.check_password = this.form.user_pwd;
          this.form.user_status = 0 ;
          let para = {user : this.form };
          console.log(para);
          this.$http.post('./api/user/register', {   //{ } json
            userInfo : para
          }, {}).then((response) => {
                console.log(response.body.code);//200  500
                  if (response.body.code == 200) {
                        this.$message({
                      message: '',
                      type: 'error'
                      });
                      this.form.user_pwd = temp_pwd;
                      this.form.check_password = temp_pwd;
                
                  } else {
                      //NProgress.done();
                      this.$message({
                      message: '',
                      type: 'success'
                      });
                     this.$router.push({ path: '/login' });  
                  }
              });
        } else {
            this.$notify({
            message: '!',
            type: 'error',
            title:''
          });
        }
      },

       //
     SellerAddSubmit: function () {
      console.log(this.form);
      this.form.server_bank = this.form.companyName;
     var temp_pwd = '';  //
        if ( this.form.user_pwd === this.form.check_password  ) {
          temp_pwd = this.form.user_pwd;
          this.form.user_pwd = hex_md5(this.form.user_pwd); //md5
          this.form.check_password = this.form.user_pwd;
          this.form.user_status = 0 ;
          let para = {user : this.form };
          console.log(para);
          this.$http.post('./api/user/SellerRegister', {   //{ } json
            userInfo : para
          }, {}).then((response) => {
                console.log(response.body.code);//200  500
                  if (response.body.code == 200) {
                        this.$message({
                      message: '',
                      type: 'error'
                      });
                      this.form.user_pwd = temp_pwd;
                      this.form.check_password = temp_pwd;
                
                  } else {
                      //NProgress.done();
                      this.$message({
                      message: '',
                      type: 'success'
                      });
                     this.$router.push({ path: '/login' });  
                  }
              });
        } else {
            this.$notify({
            message: '!',
            type: 'error',
            title:''
          });
        }
      },
  
    },
    mounted: function() {  //method  
      this.$nextTick(function() {
		
        })
    }
  }
</script>

<style>
  *{padding: 0;margin: 0;border: 0;}
	#container{
		margin:0px 0px 0px 0px ;
        padding:0px;
	}
 
	#header{
        margin: 0px;
        height: 40px;
        padding-top: 10px;
        background:#409EFF;

	}
</style>



