<template>
 <section>
  
  <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :model="filters" :inline="true" >
          <el-form-item>
          <el-input v-model="filters.name" placeholder=""></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" v-on:click="selectGoodsInfo"></el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="mock_addusers"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

  <el-table  :data="results" highlight-current-row  v-loading="listLoading" @selection-change="selsChange" style="width: 100%" label-width="0px" >
    <el-table-column type="expand">
      <template scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
          <el-form-item label="">
            <span>{{ props.row.good_money_begin + '' }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.goodIncID + '' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{  (props.row.good_income_rate * 100).toFixed(2) + '%' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{ props.row.good_date_begin.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_end.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_income.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_belong  + ''}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.count_have_sold + '' }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.goodsInDate.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="ID">
            <span>{{ props.row.userID }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.user_name }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.userPhoneNumber }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.userIDCard }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_others }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>
    <el-table-column type="selection" width="55">
      </el-table-column>
    <el-table-column
      type="index" width="60px" >
    </el-table-column>
    <el-table-column
      label=""
      prop="good_name"
      width="300" >
    </el-table-column>
    <el-table-column
      label=""
      prop="good_belonged_bank"
      width="150">
    </el-table-column>
    <el-table-column
      label=""
      prop="count_have_sold"
       >
      <template scope="scope">
       <span>{{ scope.row.count_have_sold + '' }}</span>
      </template>
    </el-table-column>
     <el-table-column
      label=""
      prop="goodStatus">
      <template scope="scope">
                    <el-tag type="success" v-if="scope.row.goodStatus == 1"  close-transition>
                        
                    </el-tag  >
                    <el-tag type="danger" v-else  close-transition>
                        
                    </el-tag  >
          </template>
    </el-table-column>

    <el-table-column label="" width="150">
          <template scope="scope">
            <el-button v-if="scope.row.goodStatus == 0" type="success" size="small" @click="handleChangeStatus( 1,scope.row)"></el-button>
            <el-button v-else type="danger" size="small" @click="handleChangeStatus(0,scope.row)"></el-button>
        </template>
       
      </el-table-column>
  </el-table> 

  <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
      </el-pagination>
     
    </el-col>

  <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules"  ref="editForm">
        <el-form-item label="" prop="number_id">
         <span>{{editForm.number_id}}</span>
        </el-form-item>

        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name" auto-complete="off"></el-input>
        </el-form-item>

       <el-form-item label="">
          <el-radio class="radio" v-model="editForm.sex" label="1"></el-radio>
            <el-radio class="radio" v-model="editForm.sex" label="0"></el-radio>
        </el-form-item>

        <el-form-item label="" type="birthday">
          <el-date-picker type="date" placeholder="" v-model="editForm.birthday "></el-date-picker>
        </el-form-item>
         <el-form-item label="" prop="native_place">
          <el-input v-model="editForm.native_place" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="editForm.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ID_card">
          <el-input v-model="editForm.ID_card" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="editForm.address"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="editForm.others"></el-input>
        </el-form-item>
      </el-form>
      
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog> 

    <!---->
    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="addForm.sex">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="">
          <el-date-picker type="date" placeholder="" v-model="addForm.birthday"></el-date-picker>
        </el-form-item>

        <el-form-item label="">
          <el-input  v-model="addForm.native_place"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input  v-model="addForm.phone"></el-input>
        </el-form-item>
        
        <el-form-item label="">
          <el-input  v-model="addForm.ID_card"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-input type="textarea" v-model="addForm.address"></el-input>
        </el-form-item> 

        <el-form-item label="">
          <el-input type="textarea" v-model="addForm.others"></el-input>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script > 
  import util from '../../common/js/util'
  import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';
  export default {
  name: 'input',      // 
  data () {
    return { 

      filters: {
          name: ''
        },
      user_ID: '',
      results : [],
      listLoading: false, 
      editFormVisible: false,//
      editLoading: false, 
      sels: [],// 
      users: [], //mockjs  
      page: 1,
      total: 0,

      editFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },

      //
        editForm: {
          number_id: 0, //
          Id_card: 0,
          name: '',
          sex:  -1,
          ID_card: '',
          birthday : '',
          native_place: '',
          phone: '',
          address: '',
          others: '',
        },

        addFormVisible: false ,//
        addLoading: false, 
        addFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        //
        addForm: {
          Id_card: 0,
          name: '',
          sex:  -1 ,
          ID_card: '',
          birthday : '',
          native_place: '',
          phone:  '',
          address: '',
          others: '',
        }
    }
  },

  methods: {

    // 
    handleCurrentChange(val) {
        this.page = val;
        this.selectGoodsInfo();
        // console.log(val);
      },

    selectGoodsInfo() {  //
      // var name = this.userName;
      this.listLoading = true;
      this.$http.post('./api/user/selectGoodsInfo', {
          page: this.page,
          name: this.filters.name
      },{}).then((response) => {
        //console.log(response.body[0].username);  
         // console.log(response.body); 
        this.results = response.body.data;
        this.total = response.body.total;
        console.log( this.results);
        this.listLoading = false;
      })
    },

     // 
    handleChangeStatus: function (status, row) { // index   index  
          console.log(status);
          console.log(row);
          var goodIncID = row.goodIncID;
          this.$http.post('./api/user/updateGoodStatus', { 
            status:    status,
            goodIncID:  goodIncID
            }, {}).then((response) => {
            this.$message({
              message: '',
              type: 'success'
            });
            this.selectGoodsInfo();
          });
    },
      //
    editSubmit: function (index, row) {
      this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
            this.editLoading = true;
            let para =Object.assign({}, this.editForm); 
            this.$http.post('./api/user/updateUser', { 
              username:     para.name,
              birthday:     para.birthday.substring(0,10),
              sex:          para.sex,
              id_card:      para.ID_card,
              native_place: para.native_place,
              address:      para.address,
              phone:        para.phone,
              others:       para.others,
              number_id:    para.number_id, 
            }, {}).then((response) => {
                this.editLoading = false;
                  //NProgress.done();
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                  this.$refs['editForm'].resetFields();
                  this.editFormVisible = false;
                  this.selectGoodsInfo();
                });
          });
          }
        });
    },
    //
    handleDel: function (index, results) { // index   index  
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          //NProgress.start()
          var num_id = results[index].number_id;
          // console.log(num_id);
          this.$http.post('./api/user/deleteUser', { 
            id:    num_id
            }, {}).then((response) => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: '',
              type: 'success'
            });
            this.selectGoodsInfo();
          });
        }).catch(() => {

        });
    },
    //
    handleEdit: function (index, row) {
        this.editFormVisible = true;
        this.editForm = Object.assign({}, row);
    },

    //
    handleAdd: function () {
        this.addFormVisible = true ;
        this.addForm = {
          Id_card: 0,
          name: '',
          sex:  -1 ,
          ID_card: '',
          birthday : '',
          native_place: '',
          phone: '' ,
          address: '',
          others: '',
        };
    },

    //
    addSubmit: function () {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              let para = Object.assign({}, this.addForm);
              // console.log(para);
             
              para.birthday = (!para.birthday || para.birthday == '') ? '' : util.formatDate.format(new Date(para.birthday), 'yyyy-MM-dd');  //


              this.$http.post('./api/user/addUser', {   //{ } json
              username:     para.name,
              birthday:     para.birthday.substring(0,10),
              sex:          para.sex,
              id_card:      para.ID_card,
              native_place: para.native_place,
              address:      para.address,
              phone:        para.phone,
              others:       para.others,
            }, {}).then((response) => {
              this.editLoading = false;
                      //NProgress.done();
                      this.$message({
                        message: '',
                        type: 'success'
                      });
                      this.$refs['addForm'].resetFields();
                      this.addFormVisible = false;
                      this.selectGoodsInfo();
                      this.addLoading = false;
            });   });
          }
        });
      },

    //mockjs 
    mock_addusers: function () { 
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true;
              //NProgress.start();
              // console.log(para);  

              let para = {
                page: this.page,
                name: this.filters.name
              };
              this.listLoading = true;
              var i;
              //NProgress.start();
              getUserListPage(para).then((res) => {
                this.total = res.data.total;
                //
                this.users = res.data.users; 
                //NProgress.done();
                // console.log(this.users[0].name); 
                 for ( i=0; i < this.users.length; i++) {
                    var _native_place = this.users[i].addr.substring(0,2); 
                    this.$http.post('./api/user/addUser', {   //{ } json
                    username:     this.users[i].name,
                    birthday:     this.users[i].birth,
                    sex:          this.users[i].sex,
                    id_card:      this.users[i].id_card,
                    native_place: _native_place,
                    address:      this.users[i].addr,
                    phone:        this.users[i].phone,
                    others:       this.users[i].other,
                  }, {}).then((response) => {});
              }
              if (i == this.users.length) {
                this.$message({
                        message: '',
                        type: 'success'
                      });
                      this.selectGoodsInfo();
                      this.addLoading = false; // 
                      this.listLoading = false;
              } 
  
                // console.log(this.total);
              });  

          });
          
       
      },
    
    selsChange: function (sels) {
        this.sels = sels;
    },

    //
    batchRemove: function () {
        var ids = this.sels.map(item => item.number_id).toString();
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          let para = { ids: ids };  //  json

          this.listLoading = false;
          this.$http.post('./api/user/batchdeleteUser', { 
            paraaaa:      para
            }, {}).then((response) => {
            this.listLoading = false;
            this.$message({
              message: '',
              type: 'success'
            });
              this.selectGoodsInfo();
          });
        }).catch(() => {

        });
      }

  },

    mounted: function() {  //method  
        this.$nextTick(function() {
          this.selectGoodsInfo();
        })
    }
}



</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .el-form-item__label{
    width: 140px;
    height: 51px;
  }
</style>


