<template>
 <section>
  
   <!---->
     <div> 
            <div id="search" >
                <el-input
                        placeholder=""  icon="search" v-model="search"  :on-icon-click="handleIconClick">
                </el-input> 
            </div>
            <div class="block"  style="float: left;margin-top: 10px;margin-left:10px">
                <el-select v-model="value" placeholder=""  clearable >
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
		    </div>
            <div>
                <el-button type="primary" style="float: left;margin-top: 10px;margin-left:10px"  @click="filterStatus" ></el-button>
            </div>
        </div>
    
      <el-table  :data="results" highlight-current-row  v-loading="listLoading" @selection-change="selsChange" style="width: 100%" label-width="0px" >
    <el-table-column type="expand">
      <template scope="props">
        <el-form label-position="left" inline class="demo-table-expand">
        <el-form-item label="">
            <span>{{ props.row.good_name }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_end.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_belong + '' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{  (props.row.good_income_rate * 100).toFixed(2) + '%' }}</span>
          </el-form-item>
          <el-form-item label="" >
            <span>{{ props.row.goodIncomeNum + '' }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_date_income.substring(0,10) }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ props.row.good_belonged_bank}}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{  props.row.moneybackCountType + ' ' +  props.row.moneybackCountNo }}</span>
          </el-form-item>
        </el-form>
      </template>
    </el-table-column>

    <el-table-column
      type="index" width="60px" >
    </el-table-column>
    <el-table-column label="" prop="orderNo"  width="140" >
    </el-table-column>
    <el-table-column label=" "  prop="apply_data"  width="250">
    </el-table-column>
    <el-table-column label="" prop="user_name">
    </el-table-column>
    <el-table-column label="" prop="apply_reason">
    </el-table-column>
     <el-table-column label="" prop="is_ok">
        <template scope="scope">
                <div v-if="scope.row.is_sold == 0">
                  <el-tag  type="danger" v-if="scope.row.is_ok == 0"  close-transition>
                                          
                                    </el-tag>
                                      <el-tag type="success" v-else-if="scope.row.is_ok == 1"  close-transition>
                                          
                                      </el-tag  >
                                      <el-tag type="danger" v-else  close-transition>
                                          
                        </el-tag >
                </div>
                <div v-else>
                    <el-tag type="success"   close-transition>
                        
                    </el-tag  >
                </div>
                
          </template>
    </el-table-column>
    
    <el-table-column label="" width="150">
        <template scope="scope">
          <el-button type="success"  size="small" :disabled="(scope.row.is_sold == 1)" @click="handleAgree(scope.row)"></el-button>
          <el-button type="danger" size="small" :disabled="(scope.row.is_sold == 1)"  @click="handleRefuse(scope.row)"></el-button>
        </template>
      </el-table-column>
  </el-table> 
    
    <div style="margin-top:30px;margin-left:20px">
                    
                    
                    1
                    2
                    3
                    4  
                </div>
  </section>
</template>

<script > 
  export default {
  name: 'input',      // 
  data () {
    return { 

      isUsed : true,
      checkBoxValue: [], // 
      listLoading: false, 
       search :'', 
      u_name :'',
      filters: {  //
          name: ''
        },
      results : [],  //
      finds: [] ,  // 
      sels: [],// 
      page: 1,
      total: 0,
      options: [{
        value: '1',
        label: ''
        }, {
        value: '2',
        label: ''
        }, {
        value: '3',
        label: ''
        }, {
        value: '4',
        label: ''
        }],
      value: '',
    }
  },

  methods: {

    
    StatusOperation(flag,status){
      if(flag == 1){
          for(var index = 0; index < this.results.length ; ){
            var element = this.results[index].is_sold;
            if (element == status ) {
                index ++;
                continue;
            }else{
                this.results.splice(index,1);
            }
        }
      }else{
        for(var index = 0; index < this.results.length ; ){
            var element = this.results[index].is_ok;
            if (element == status ) {
                index ++;
                continue;
            }else{
                this.results.splice(index,1);
            }
        }
      }
        
    },

    filterStatus(){ //
        var val = this.value;
        if (this.results.length != 0 && this.value!='') {
              switch (val) {
            case '1':
                this.StatusOperation(1,1); //
                break;
            case '2':
                this.StatusOperation(2,0);//
                break;
            case '3':
                this.StatusOperation(2,1); //
                break;
            case '4':
                this.StatusOperation(2,-1);//
                break;
            }
        }else{
              this.selectResellInfo();
        }
    },

    handleIconClick() { //
      console.log(this.search);
      var key = this.search;
        if ( (key  == '' || this.results.length ==0 )  ) {
                  this.selectGoodsInfo();
          }
          else{
                for (var i = 0  ; i < this.results.length ; ) {
                    if ( this.results[i].good_name.toString().search(key) != -1 || this.results[i].good_belonged_bank.toString().search(key) != -1 ) 	  
                        {
                            i++;
                            continue;
                        }
                        else
                        {
                            this.results.splice(i,1);
                            //  length  
                        }
                      }
                  }
  },


    selectResellInfo() {   //
      // var name = this.userName;
      this.listLoading = true;
      this.$http.post('./api/user/selectResellInfo', {
          u_name : this.u_name,
      },{}).then((response) => {
        var orderResult = response.data; //
        for (var  index = 0; index < orderResult.length; index++) {
              var element = orderResult[index];
              element.apply_data = new Date(element.apply_data).toLocaleString();
        }
        this.results = orderResult;
        console.log(this.results);
        this.listLoading = false;
      })
    },

    handleRefuse(row){ //
        var isOk = -1;
        this.handleUpdate(row,isOk);
    },
    handleAgree(row){ //
        var isOk = 1;
        this.handleUpdate(row,isOk);
    },
    //
    handleUpdate(row,isOk) { // index   index  
         
        console.log(row);
        var resell_id = row.resell_id; //id 
        var is_ok = isOk;
        this.$http.post('./api/user/updateResellOk', { 
        resell_id:    resell_id,
        is_ok:        is_ok
        }, {}).then((response) => {
        //NProgress.done();
        this.$message({
            message: '',
            type: 'success'
        });
        this.selectResellInfo();
        });
    },
    
    selsChange: function (sels) {
        this.sels = sels;
    }
 
  },
    mounted: function() {  //method     
      this.$nextTick(function() {
         var user = sessionStorage.getItem('user');
         if (user) {
          user = JSON.parse(user);
          this.u_name = user.u_user || '';
          };
          this.selectResellInfo();
        })
    }
}
</script>

<style>
</style>


