<template>
  <div>
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form  :inline="true" >
          <el-form style="float:left;margin-right:10px">
          <div class="block" >
            <el-date-picker
              v-model="datetimeRange"
              type="daterange"
              align="right"
              placeholder=""
              :picker-options="Time_pickerOptions">
            </el-date-picker>
		      </div>
        </el-form>

        <el-form-item>
          <el-button type="primary" v-on:click="findSomething"></el-button>
        </el-form-item>

      </el-form>
    </el-col>
        <div >
            <el-table :data="orderResult" stripe style="width: 100%">
              <el-table-column  type="index"  width="50">
              </el-table-column>
              <el-table-column  prop="orderNo" label="" width="130">
                <template scope="scope">
                  <el-button  style="margin-top:1px;" type="text" @click="viewDetail(scope.row)">{{scope.row.orderNo}}</el-button>
                </template>
              </el-table-column>
              <el-table-column  prop="good_name" label="" width="170">
              </el-table-column>
              <el-table-column
                prop="goodCnt"
                label=""
                width="110">
                <template scope="scope">
                  {{scope.row.goodCnt + ''}}
              </template>
              </el-table-column>
                <el-table-column prop="goodIncomeNum" label=""  width="100">
                    <template scope="scope">
                      {{scope.row.goodIncomeNum + ''}}
                    </template>
              </el-table-column>
              <el-table-column prop="good_date_income" label="" width="120" >
                <template scope="scope">
                  {{(scope.row.good_date_income).substring(0,10)}}
                </template>
              </el-table-column>
              <el-table-column
                prop="orderCreateDatetime"
                label="">
              </el-table-column>
              <el-table-column  label=""  width="100">
                <template scope="scope">
                  <div v-if="scope.row.have_resell == 0">
                     <div v-if="scope.row.haveFinshed == 1">
                              <el-button type="success"  :disabled=" scope.row.orderStatus==0 "  size="small"  @click=""></el-button> 
                    </div>
                    <div v-else-if= "scope.row.haveFinshed == 2 ">
                        <el-tag type="success"  close-transition>
                            
                        </el-tag  >   
                    </div>
                    <div v-else>
                          <el-popover trigger="hover" placement="top">
                            <p></p>
                            <div slot="reference" class="name-wrapper">
                              <el-tag></el-tag>
                            </div>
                          </el-popover>
                    </div>
                  </div>
                  <div v-else-if="scope.row.have_resell == 1">
                      <el-tag type="success"  close-transition>
                           
                      </el-tag  >  
                  </div>
                    <div v-else-if="scope.row.have_resell == 2">
                      <el-tag type="success"  close-transition>
                           
                      </el-tag  >  
                  </div>
                </template>
              </el-table-column>
            </el-table>
        </div>


    <div>
           <!--  -->
             <el-dialog title="" v-model="OrderDetailFormVisible"   :close-on-click-modal="false"  size="large" >
              <h3></h3>
            <el-table :data="showResults"  style="width: 100%; margin-bottom:10px">
          <el-table-column  fixed  prop="good_name" label=""  width="220">
          </el-table-column>
          <el-table-column  label="" width="200px">
                  <template scope="scope">
                        <el-tag type="primary" v-if="scope.row.is_resell == 1"  close-transition>
                          
                      </el-tag>
                      <el-tag type="success" v-else  close-transition>
                          
                      </el-tag>
                      <el-tag type="success" v-if="scope.row.orderStatus == 1"  close-transition>
                          
                      </el-tag>
                      <el-tag type="danger" v-else  close-transition>
                          
                      </el-tag>
                      <el-tag type="success" v-if="scope.row.have_resell == 1"  close-transition>
                          
                      </el-tag>
                  </template>
            </el-table-column>
           <el-table-column   label=""  width="120">
              <template scope="scope">
                <!-- 1  2   3   4  -->
                   <el-button size="small" type="warning" :disabled="scope.row.canResell ||(scope.row.have_resell == 1)|| (scope.row.have_resell == 2) || (scope.row.orderStatus == 0) || (scope.row.is_resell == 1)" @click="handleResell(scope.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column prop="good_date_belong"  label=""  width="120">
              <template scope="scope">
                    {{scope.row.good_date_belong + ''}}
              </template>
          </el-table-column>
          <el-table-column  label="" width="120">
            <template scope="scope">
                {{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}
            </template>
          </el-table-column>
          <el-table-column  prop="goodCnt"  label="" width="120">
            <template scope="scope">
                {{scope.row.goodCnt + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="sumIncome"  label=""  width="120">
            <template scope="scope">
                {{scope.row.goodIncomeNum + ''}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_end" label=""  width="150">
            <template scope="scope">
                {{scope.row.good_date_end.substring(0,10)}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_income"  label=""  width="120">
              <template scope="scope">
                {{scope.row.good_date_income.substring(0,10)}}
            </template>
          </el-table-column>
          <el-table-column  prop="good_date_income"  label=""  width="120">
              <template scope="scope">
                {{scope.row.orderCreateDatetime.substring(0,10)}}
            </template>
          </el-table-column>

        </el-table>
      <!--  -->
      <h3></h3>
        <el-table  :data="user"   border style="width: 100%;">
            <el-table-column  prop="user_name" label="" width="180">
            </el-table-column>
            <el-table-column prop="login_name"  label=""  width="180">
            </el-table-column>
            <el-table-column prop="userIDCard"  label=""  width="180">
            </el-table-column>
            <el-table-column   prop="userPhoneNumber" label="">
            </el-table-column>
            <el-table-column   prop="sumIncome"    label="">
              <template scope="scope">
                {{scope.row.sumIncome + ''}}
                </template>
            </el-table-column>
              <el-table-column  label="">
                        <template scope="scope">
                            <el-tag  type="primary"  close-transition>
                                {{scope.row.moneybackCountType}}
                            </el-tag>
                              {{" " + scope.row.moneybackCountNo}}
                        </template>
                    </el-table-column>
        </el-table>
        <div slot="footer" class="dialog-footer" align ="center" >
            <el-button @click.native="OrderDetailFormVisible = false"></el-button>
        </div>

        <div style="margin-top:30px;margin-left:20px">
            
            <br>
            1<br>
            2<br>
            3<br>
            4<br>
            5<br>
      </div>
 </el-dialog> 

    </div>
      <div style="margin-top:30px;margin-left:20px">
                    
                    
                    1
                    2
                    3
                  
      </div>
  </div>
</template>

<script>

  import YShelf from '../../common/shelf'

  export default {
    data () {
      return {
        orderResult:[], //
        orderNoResult:[],//  
        showResults:[],
        user:[],  //
        OrderDetailFormVisible: false,
        userId:'',  // userid 
        datetimeRange: [],
      Time_pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
      }
    },
    methods: {
        
      viewDetail(row){ // 
   
            this.showResults.length = 0;
            this.showResults.push(row);
            this.user[0].sumIncome = row.goodCnt;
            this.OrderDetailFormVisible = true;
            console.log(row);

      },
      selectOrderList(){ //id
        this.$http.post('./api/user/selectOrderList', {
            userID : this.userId
        },{}).then((response) => {
            // console.log(response.data);
            var orderResult = response.data;
            var dateNow = new Date().getTime(); //

            for (var  index = 0; index < orderResult.length; index++) {
              var element = orderResult[index];
              
          
              element.orderCreateDatetime = new Date(element.orderCreateTime).toLocaleString(); //
              var DateOrder = new Date(element.good_date_income).getTime();
              // console.log(DateOrder); 
              if(DateOrder > dateNow ){  // 
                    element.canResell = false; //  
                    
              }else{
                    element.canResell = true; // 
                    if(element.haveFinshed == 0){
                      this.updateHaveFinish(1,element.orderID);
                    }
              }
              this.orderResult = orderResult;
             
           } console.log(this.orderResult);
        })
      },

       findSomething() {  //
        var flag = false;
        if (this.datetimeRange.length != 0) {
          var dateStart = this.datetimeRange[0]; //
          var dateEnd = this.datetimeRange[1]; 
          var date;
          var str;
          console.log(this.datetimeRange);
          
          flag =true;
    		  for (var i = 0  ; i < this.orderResult.length ;) {  //
    			str = this.orderResult[i].orderCreateTime;  //
          date = new Date(str);
          console.log(date);
          
          if (dateStart <= date  &&  dateEnd >= date) {
                i++;
                continue;
            }else{
              this.orderResult.splice(i,1);
            }
        }
          this.datetimeRange.length = 0;
        }
        if ( this.datetimeRange.length == 0 && !flag ) {
            this.selectOrderList();
        }
    },
      updateHaveFinish(status,orderID){  //
        this.$http.post('./api/user/updateHaveFinish', {
                orderID : orderID,
                status: status,
            },{}).then((response) => {
              this.selectOrderList(); //
            })
      },
      handleResell(row){ // 1  2 
          // console.log(row);
          sessionStorage.setItem('resellGoodsInfo', JSON.stringify(row) );//session
          this.$router.push({ path: '/Resellpage' });
      }
    },
    components: {
      YShelf
    },
    mounted () {
       var user = sessionStorage.getItem('user');
        if (user) {
        user = JSON.parse(user);
        this.userId = user.userID;
        this.user.push(user);
        this.selectOrderList();
        }
    },
 
  }
</script>


<style lang="scss" scoped>
  @import "../../assets/style/mixin";

   .el-table .info-row {
    background: #c9e5f5;
  }
  .el-table .positive-row {
    background: #e2f0e4;
  }
</style>


