<template>
<div style="margin-top:10px">
  <div v-show="info">

    <div  class="bg-purple">
        <span style="font-size: 20px;margin-left:20px;" >   ,{{user.user_name}} </span>
    </div>

    <div class="bg-purple-light">
        <div class="text-r-out-in" style="margin-bottom:10px"></div>
        <el-row type="flex" >
                <el-col :span="6"><div class="grid-content ">
                    <span style="color:#13CE66 ; margin-left:10px"> {{MoneySum}} </span>
                    </div></el-col>
                <el-col :span="6"><div class="grid-content ">
                    <span style="color:#20A0FF">{{goodCnt}} </span>
                    </div></el-col>
                <el-col :span="6"><div class="grid-content ">
                    <span style="color:#13CE66">{{goodIncomeNum}}</span>
                    </div></el-col>
            </el-row>
            <el-row type="flex" >
                <el-col :span="6"><div class="grid-content">
                <span style="color:#F7BA2A">{{goodsunResellNum}}</span>
                    </div></el-col>
                <el-col :span="6"><div class="grid-content ">
                    <span style="color:#F7BA2A">{{freezeMoney}}</span>
                    </div></el-col>
                <el-col :span="6"><div class="grid-content ">
                        
                    </div></el-col>
            </el-row>       
    </div>
    
    <div class="bg-purple">
        <div class="text-r-out-in" style="margin-bottom:10px" ></div>
        <el-row type="flex" >
            <el-col :span="6"><div class="grid-content ">
                {{user.login_name}}
                </div></el-col>
            <el-col :span="6"><div class="grid-content ">
                {{user.userPhoneNumber}}
                </div></el-col>
            <el-col :span="6"><div class="grid-content ">
                {{user.userIDCard}}
                </div></el-col>
        </el-row>
        <el-row type="flex" >
            <el-col :span="6"><div class="grid-content ">
            {{user.moneybackCountType}} {{user.moneybackCountNo}}
                </div></el-col>
            <el-col :span="6"><div class="grid-content ">
                {{user.user_register_time}}
                </div></el-col>
            <el-col :span="6"><div class="grid-content ">
                <el-button type="primary" size="small" @click="alert = true; info=false;"></el-button>
                <el-button type="primary" size="small" @click="alertPassword = true; info=false;"></el-button>
            </div></el-col>
        </el-row>
    </div>
    
</div>
    <div v-show="alert">
            <div class="box-l">
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"> </div>
                <div class="text-l" style="padding-bottom:10px"></div>
            </div>
            <div class="box-m">
                <div class="text-m">{{user.user_name}}</div>
                <div class="text-m">{{user.login_name}}</div>
                <div class="text-m inputStyle"><el-input v-model="userPhoneNumber" placeholder="" style="width:217px"></el-input></div>
                <div class="text-m ">{{user.userIDCard}}</div>
                <div class="text-m inputStyle"> 
                    <el-select v-model="payTab" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select> 
                </div>
                <div class="text-m"><el-input v-model="userMoneyBackcount" placeholder="" style="width:217px"></el-input></div>
                <div>
                <el-button type="primary" @click="updateUserInfo"></el-button>
                </div>
            </div>
    </div>
     <div v-show="alertPassword">
            <div class="box-l">
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
                <div class="text-l"></div>
            </div>
            <div class="box-m">
                <div class="text-m">{{user.user_name}}</div>
                <div class="text-m">{{user.login_name}}</div>
                <div class="text-m inputStyle"><el-input v-model="oldPassword"  type="password" placeholder="" style="width:217px"></el-input></div>
                <div class="text-m"><el-input v-model="newPassword"  type="password" placeholder="" style="width:217px"></el-input></div>
                <div>
                    <el-button type="primary" :disabled="!oldPassword || !newPassword " @click="updateUserPassword"></el-button>
                </div>
            </div>
    </div>
</div>
    
</template>

<script>
export default {
    data() {
      return {
        OrderResults: [], //
        user:'',
        info:true,
        alert:false,
        goodCnt:0,//
        goodIncomeNum:0, //
        goodsunResellNum:0,//
        MoneySum:'', //
        freezeMoney:'', //
        userPhoneNumber:'',
        userMoneyBackcount:'',
        userId:'',  //id
        payTab :'',
        alertPassword:'',//
        user_pwd:'',
        oldPassword:'',//
        newPassword:'',//

      }
    },
    methods:{
        selectOrderList(){ //id
        this.$http.post('./api/user/selectOrderList', {
            userID : this.userId
        },{}).then((response) => {
            console.log(response.data);
            var OrderResults = response.data;
            var goodCnt = 0;
            var goodIncomeNum = 0;
            var freezeMoney = 0;
            var date = new Date().getTime(); //
            for(var index = 0; index < OrderResults.length ;index ++ ){
                var element  = OrderResults[index];
                goodCnt = goodCnt + element.goodCnt;
                if (element.haveFinshed == 2) { //
                     goodIncomeNum = goodIncomeNum + element.goodIncomeNum;
                }
                if(element.orderStatus == 0){ //==0 
                    freezeMoney = freezeMoney + element.goodCnt ; //
                    if( element.haveFinshed == 2){ //  
                        freezeMoney = freezeMoney +  element.goodIncomeNum;
                    }
                }
            }
            this.goodIncomeNum = goodIncomeNum;
            this.goodCnt = goodCnt.toFixed(2);
            //
            this.MoneySum = goodIncomeNum + goodCnt - this.goodsunResellNum;//
            this.freezeMoney = freezeMoney;
            if(this.MoneySum != this.user.user_property){ //
                this.updateUserProperty();
            }
        })
      },

        updateUserProperty(){//  
            this.$http.post('./api/user/updateUser_property', {
                userID : this.userId,
                User_property: this.MoneySum
            },{}).then((response) => {})
        },

        selectResellResult(){ //
                this.$http.post('./api/user/selectResellResult', {   //{ } json
                    userId:                this.user.userID
                }, {}).then((response) => {
                var ResellResult = response.data;
                var sum = 0;
                for (var index = 0; index < ResellResult.length; index++) {
                    var element = ResellResult[index];
                    if(element.is_sold == '1'){
                        sum = sum + element.have_bought_number
                    }
                }
                this.goodsunResellNum = sum;
            }); 
        },

       updateUserInfo(){// 
            this.$http.post('./api/user/updateUserInfo', {
            userPhoneNumber:this.userPhoneNumber,
            payTab: this.payTab,
            userMoneyBackcount: this.userMoneyBackcount,
            userID : this.userId,
            User_property: this.MoneySum
        },{}).then((response) => {
             this.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        this.alert = false;
        this.info = true;
        })
      },

      updateUserPassword(){// 
        var md5_oldPwd = hex_md5(this.oldPassword);
        console.log(md5_oldPwd);
        console.log(this.user_pwd);
        
        if (md5_oldPwd === this.user_pwd) {
            this.newPassword = hex_md5(this.newPassword);
            this.$http.post('./api/user/updateUserPassword', {
            user_pwd: this.newPassword,
            userId: this.userId
        },{}).then((response) => {
            this.$message({
          showClose: true,
          message: ',',
          type: 'success'
        });
         this.$router.push({ path: '/indexlogin' });
        this.alertPassword = false;
        this.info = true;
        })
        } else {
            this.$message({
            showClose: true,
             message: '',
            type: 'error'
        });
        }
          
      },

    },
     mounted() {
            var user = sessionStorage.getItem('user');
            if (user) {
            this.user = JSON.parse(user);
            console.log(this.user);
            this.userId = this.user.userID;
            this.userPhoneNumber = this.user.userPhoneNumber;
            this.userMoneyBackcount = this.user.moneybackCountNo
            this.user.user_register_time = this.user.user_register_time.substring(0,10);
            this.user_pwd = this.user.user_pwd; //
            // console.log(this.user);
            this.selectResellResult();
            this.selectOrderList();
        }
     }
}
</script>

<style lang="scss" scoped rel="stylesheet/scss">
   .text-l{
       margin-bottom: 30px;
       text-align:right;
       font-family: Microsoft YaHei;
       font-size: 20px;
   }
   .box-l{
    //    border-left: 1px solid #ccc;
    //    border-top: 1px solid #ccc;
    //    border-bottom: 1px solid #ccc;
       width:120px;
       float: left;
   }
   .box-m{
    //    border-right: 1px solid #ccc;
    //    border-right: 1px solid #ccc;
    //    border-top: 1px solid #ccc;
    //    border-bottom: 1px solid #ccc;
       width:250px;
       float: left;
    //    margin-left: 20px;
   }

    .text-m{
       margin-bottom: 30px;
       text-align:left;
       font-family: Microsoft YaHei;
       font-size: 20px;
   }

   .inputStyle{
        margin-bottom: 20px;
   }

   .box-r{
        margin-left: 60px;
        width: 100%;
   }

   .text-r-moneySum{
       font-family: Microsoft YaHei;
       font-size: 20px;
       padding-left: 30px;
   }
   .text-r-out-in{
        font-family: Microsoft YaHei;
        font-size: 20px;
        margin-top: 20px;
   }

     .el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
    padding: 20px;
  }
  .bg-purple-light {
    background: #e5e9f2;
    padding: 20px;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>




