<template>

 <section>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title"></h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="ruleForm.account" auto-complete="off" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="ruleForm.checkPass" auto-complete="off" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
    <el-checkbox v-model="checked" checked class="remember"></el-checkbox>
    <el-button  style="float:right" type="text" @click="handleAdd"></el-button>
    </el-form-item>

    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmitLogin" :loading="logining"></el-button>
      <!--<el-button @click.native.prevent="handleReset"></el-button>-->
    </el-form-item>

    
    <el-form-item  align ="center"> 
        <el-button  type="text" @click="handleBack"></el-button>
    </el-form-item>
  </el-form>


</section>
</template>

<script>
  import { requestLogin } from '../api/api';
  //import NProgress from 'nprogress'
  export default {
    data() {
      return { 

     
        addLoading: false, 
        Id_card : '',
        inputIsOk: true,
        logining: false,

        isActive: true,  //  


         addFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
       // 
        addForm: {
          user_name:'',  //
          userIDCard:'',//
          userPhoneNumber:'',//
          user_pwd:'', //
          moneybackCountNo:'', //
          server_bank: '', //
          login_name: '',     //
          check_password :'', //
          server_bank:''
        },
        
        userTypeOptions: [{ //
          value: 'seller',
          label: ''
        }, {
          value: 'root',
          label: ''
        }],

        ruleForm: {
          account: 'root',
          checkPass: '123456'
        },
        rules: {
          account: [
            { required: true, message: '', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {
    
      handleSubmitLogin(ev) {  //
        // var _this = this;
        var temp_pwd = '';
        this.$refs.ruleForm.validate((valid) => {
          if (valid) {
            // console.log(this.ruleForm.checkPass);
            temp_pwd = this.ruleForm.checkPass;
            this.ruleForm.checkPass = hex_md5(this.ruleForm.checkPass); //md5
            this.$http.post('./api/user/manage_login', {   //{ } json
                username: this.ruleForm.account, 
                password: this.ruleForm.checkPass
            }, {}).then((response) => {
              console.log(response);
              let { code, msg, user } = response.data;
              if (code == 500) {
                this.$message({
                  message: msg,
                  type: 'error'
                });
                this.ruleForm.checkPass = temp_pwd;
              }else if(code == 300){
                this.$notify({
                  title: '',
                  message: msg ,
                  duration: 0
                 });
                this.ruleForm.checkPass = temp_pwd;
              } else {
                console.log(this.ruleForm.account);
                user.u_user = this.ruleForm.account;
                sessionStorage.setItem('user', JSON.stringify(user) );
                // sessionStorage.setItem('u_user', this.ruleForm.account ); //  {u_username: "admin", u_role: 1, u_user: "admin"}
                  // console.log(user);
                   this.$router.push({ path: '/home' });
        
              }
            });

          } else {
            console.log('error submit!!');
            return false;
          }

        });
      },
       // 
      handleBack(){
          this.$router.push({ path: '/' });
      },

      handleAdd() { //
         this.$router.push({ path: '/SellerRegister' });
      }, 

    },
    watch:{  //@change 

    },

  }

</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 20px 35px 0px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 0px 0px;
    }
  }


</style>


