<template>
<div id="cartstyle">
      <div id="page">
        <el-table ref="multipleTable" :data="cart_result" border tooltip-effect="dark" 
            v-loading="listLoading"  @selection-change="handleSelectionChange">
                  <el-table-column  width="55">
                      <template scope="scope" >
                        <!-- `checked`  true  false -->
                        <el-checkbox v-model="scope.row.IsChoose" @change = "handleChangeChoose(scope.row)" ></el-checkbox>
                      </template>
                  </el-table-column>
                  <el-table-column label="" width="330">
                    <template scope="scope">{{ scope.row.good_name }}</template>
                  </el-table-column>

                   <el-table-column  label="" width="120">
                                <template scope="scope">
                                    <el-tag  type="primary"  close-transition>
                                        {{scope.row.goodIncID}}
                                    </el-tag>
                                </template>
                    </el-table-column>
                  <el-table-column label="" width="120">
                    <template scope="scope">{{ scope.row.good_date_belong + ''}}</template>
                  </el-table-column>
                  <el-table-column label="" width="120">
                    <template scope="scope">{{ (scope.row.good_income_rate * 100).toFixed(2) + '%' }}</template>
                  </el-table-column>
                  <el-table-column label="" width="160">
                    <!-- <template scope="scope">{{ scope.row.goodCnt + ''}}</template> -->
                    <template scope="scope" >
                      <el-input-number size="small"  :step ="scope.row.good_inc_money" :disabled="incIsOk"   :debounce= "1000" @change="handleNumChange(scope.row)" v-model="scope.row.goodCnt"></el-input-number>
                    </template>
                  </el-table-column>
                  <el-table-column  prop="address" label=""  show-overflow-tooltip>
                    <template scope="scope">
                            <el-button size="small" type="danger" @click="handleDel(scope.row)"></el-button>
                    </template>
                  </el-table-column>
            </el-table>
      </div>
            <el-row>
              <el-col :span="24"><div class="grid-content bg-purple-dark">
                  <el-col :span="24" class="toolbar">
                            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="7" :total="total" style="float:right;">
                            </el-pagination>
                  </el-col>
                 </div></el-col>
          </el-row>

           <el-row :gutter="20" >
            <el-col :span="3"><div class="grid-content bg-purple">
                   <!-- <el-button type="success" width=90 @click="chooseAll"></el-button>   -->
            </div></el-col>
            <el-col :span="9"><div class="grid-content bg-purple"></div></el-col>
            <el-col :span="9"><div class="grid-content bg-purple" style="padding-top:8px;padding-left:0px">
                  <span style="padding-left:20px">:
                    <span style="color:red">
                        {{this.selectGoodsNum +''}}
                    </span>
                  </span>

                  <span style="padding-left:20px" >:
                    <span style="color:red">
                        {{'' + this.sum +''}}
                    </span>
                  </span>
            </div></el-col>
            <el-col :span="3"><div class="grid-content bg-purple"  >
              <el-button type="success"  @click="submit_order"></el-button>  
            </div></el-col>
        </el-row>
     
  </div>
</template>

<script>
  export default {
    data() {
      return {
        checked: true,
        cart_result: [],
        multipleSelection: [],  //
        sum : 700000,
        userId:'',//ID
        user_authority:'',//
        listLoading:false,
        page: 1,
        total: 20,  // 
        incIsOk:false,
        selectGoodsNum :0,
        flag : true, //
      }
    },

    methods: {
      selectGoods() {
              this.listLoading = true;
              this.$http.post('./api/user/selectCartInfo', {
                  userId: this.userId,
                  page: this.page
              },{}).then((response) => {
                  this.cart_result = response.data.data;
                  this.listLoading = false;
                  this.total = response.data.total;
                  // console.log(this.cart_result);
                  var sum = 0;
                  var cnt = 0; //
                  for(var i = 0 ; i < this.cart_result.length ; i ++ )
                  {    

                      if(this.cart_result[i].IsChoose == 0) this.cart_result[i].IsChoose  = false;
                      else {  // 
                          this.cart_result[i].IsChoose  = true;
                           sum  = sum + this.cart_result[i].goodCnt;
                           cnt ++ ;
                      } 
                  }
                  this.sum = sum ;
                  this.selectGoodsNum = cnt;
              })
          },
       handleCurrentChange(val) {  //  
            this.page = val;
            this.selectGoods();
            },
      handleSelectionChange(val) { 
        this.multipleSelection = val;
      },

      handleChangeSelectCart(row){ //
        console.log(row);   
        var cartID = row.cartID;
        var IsChoose = row.IsChoose?1:0;
        console.log(IsChoose);
        this.$http.post('./api/user/updateCartChoose', { 
          cartID:     cartID,
          IsChoose:   IsChoose
        }, {}).then((response) => {
          console.log(response);
            });
      },
      chooseAll(){ //
          var sum = 0;
          if(this.flag) //true
          {
              for(var i = 0 ; i < this.cart_result.length ; i ++ )
              { 
                  this.cart_result[i].IsChoose = true ;
                  sum  = sum + this.cart_result[i].goodCnt;
              }
              this.flag = false;
              this.sum = sum;
              this.selectGoodsNum =  i ;
          }else{
              for(var i = 0 ; i < this.cart_result.length ; i ++ )
              { 
                  this.cart_result[i].IsChoose = false ;
              }
              this.flag = true;
              this.sum = sum;
              this.selectGoodsNum =  0;
          }
          
      },
      handleChangeChoose(row){  //
          // console.log(row);
          this.handleChangeSelectCart(row);
          // this.row = row;
          // setTimeout(this.handleChangeSelectCart(row),"1000");//
          var sum = 0; // 
          var cnt = 0; //
          for(var i = 0 ; i < this.cart_result.length ; i ++ )
          { 
              if(this.cart_result[i].IsChoose == true )
              {
                  sum  = sum + this.cart_result[i].goodCnt;
                  cnt ++ ;
              }
          }
          // console.log(sum);
          this.sum = sum ;
          this.selectGoodsNum = cnt;
      },
      submit_order(){
          var cart_result = new Array();
          for (let index = 0; index < this.cart_result.length; index++) {
                if (this.cart_result[index].IsChoose) {
                  cart_result.push(this.cart_result[index])
                }
          }
          if(cart_result.length > 4 || cart_result.length <= 0){
               this.$notify.error({
                title: '',
                message: ''
              });
          }else{
            sessionStorage.setItem('goods', JSON.stringify(cart_result) );
            this.$router.push({ path: '/Checkout' });
          }
          
      },
        //
    handleDel: function (cart_result) { // index   index  
        // console.log(cart_result);
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true;
          //NProgress.start()
          var cartID = cart_result.cartID;

          this.$http.post('./api/user/deleteCartGoods', { 
            cartID:    cartID
            }, {}).then((response) => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: '',
              type: 'success'
            });
            this.selectGoods();
          });
        }).catch(() => {

        });
    },
    handleNumChange(cart_result){ // 
        var cartID = cart_result.cartID;
        var goodCnt = cart_result.goodCnt +cart_result.good_inc_money; //
        var cartID = cart_result.cartID;
        var cartNum =cart_result.goodCnt;
        this.$http.post('./api/user/updateCartNumber', { 
          cartID:    cartID,
          cartNum:   goodCnt
        }, {}).then((response) => {
          console.log(response) ;
          });
      },
    },
     mounted() {
            var user = sessionStorage.getItem('user');
            if (user) {
            this.user_authority = JSON.parse(user);
            this.userId = this.user_authority.userID; //ID          
            }
            this.selectGoods();
            // this.autoSelectRow([this.cart_result[1], this.cart_result[2]]);
		}
  }
</script>
<style>
    	#cartstyle{
        width: 1000px;
        margin: 0 auto;
        height: 472px;
      }

      #page{
        margin: 0 auto; 
	    } 
      .el-row {
          margin-bottom: 10px;
          width: 1000px;
          /* margin-left: 180px; */
        
      }
   
      .grid-content {
        border-radius: 4px;
        min-height: 36px;
      }
      .row-bg {
        padding: 10px 0;
        background-color: #f9fafc;
      }
</style>



